import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class NavigateToService {

  constructor() {
  }

  navigateTo(URL: string, params?: string): void {
    // window.location.href = URL;
   const link: string = params ? URL + params : URL;
   window.open(link, '_blank');
  }
}
