import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {MatCheckboxChange} from '@angular/material/checkbox';
import {CStaticIds} from '../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-order-search',
  templateUrl: './order-search.component.html',
  styleUrls: ['./order-search.component.css']
})
export class OrderSearchComponent implements OnInit {
  orderSearchForm!: FormGroup;
  orderSummaryList: string[] = [];
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param formBuilder for using FormBuilder
   * @param data for dialog data
   */
  constructor(
    public dialogRef: MatDialogRef<OrderSearchComponent>,
    private formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.orderSearchForm = this.formBuilder.group({
      orderSummaryList: [this.orderSummaryList],
      subOrderType: [''],
      dateStart: [''],
      dateEnd: ['']
    });
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * show search options
   * @param event for MatCheckboxChange
   * @param value
   * @return void
   */
  showOptions(event: MatCheckboxChange, value: string): void {
    if (event.checked) {
      this.orderSummaryList.push(value);
    } else {
      const index = this.orderSummaryList.findIndex(c => c === value);
      this.orderSummaryList.splice(index, 1);
    }
    this.orderSearchForm.controls.orderSummaryList.updateValueAndValidity();
  }
  /**
   * onSubmit check form validation
   * @return void
   */
  onSubmit(): void {
    if (!this.orderSearchForm.valid) {
      return;
    } else {
      this.dialogRef.close(this.orderSearchForm.value);
    }
  }
}
