import { Component, OnInit, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-about-digital-service',
  templateUrl: './about-digital-service.component.html',
  styleUrls: ['./about-digital-service.component.css'],
})
export class AboutDigitalServiceComponent implements OnInit {
  dataSetionList: string[] = [
    'Custom Implant abutment from Scanning Body including Model',
    'Custom healing abutment',
    'Implant Crown including Model (From Scan Body)',
    'Crown including Model (On Prepped Tooth)',
    'Complete Denture',
  ];
  constructor(
    public dialogRef: MatDialogRef<AboutDigitalServiceComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {}

  ngOnInit(): void {}

  cancel(): void {
    this.dialogRef.close();
  }
}
