import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { APIName } from 'shared/API/APIName';
import { IMainResponse } from '../../../Models/Interfaces/Main/MainResponse';
import { OrderCost } from '../../../Models/Interfaces/Order/OrderCostResponse.model';
import { DataService } from '../../DataService/data.service';

@Injectable({
	providedIn: 'root'
})
export class AddOrderService {
	calculateCost(data:any):Observable<OrderCost> {
		return this.dataService.post(APIName.calculateCost, data).pipe(map((res:IMainResponse)=>{
			return JSON.parse(res.responsString) as OrderCost;
		}));
	}

	constructor(private dataService: DataService) { }

	addOrder(order: any): Observable<IMainResponse> {
		return this.dataService.post(APIName.ORDERS, order);
	}
	uploadOrderFile(data:any,orderId:any): Observable<IMainResponse> {
		return this.dataService.post(`${APIName.uploadFile}?userID=${localStorage.getItem('userCNId')}&orderID=${orderId}`, data);
	}
}
