import { Component, Input, OnInit, DoCheck, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatRadioChange } from '@angular/material/radio';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';

/**
 * Copyright (c) 3ddx
 *
 * @summary Conversion Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-11-24
 */
@Component({
  selector: 'app-conversion-form',
  templateUrl: './conversion-form.component.html',
  styleUrls: ['./conversion-form.component.css']
})
export class ConversionFormComponent implements OnInit, DoCheck {
  @Input() conversion!: FormGroup;
  @Input() surgicalGuide!: FormGroup;
  @Input() moreThan!: boolean;
  @Input() maxillaAndMandible!: boolean;
  @Input() checkModelScan!: boolean;
  @Output() changeValue: EventEmitter<Function> = new EventEmitter<Function>();
  software: number = 1;
  maxilla: number = 20;
  mandible: number = 20;
  modelScan: number = 0;
  oral: number = 1;
  htmlID = CStaticIds;
  mandibleCheck!: boolean;
  maxillaCheck!: boolean;

  @Input() expressSoftware!: boolean;
  constructor(
    private fb: FormBuilder,
    public orderService: OrderSharedService
  ) {
  }

  ngOnInit(): void {
    this.getSoftwareValue();
    this.getMaxillaValue();
    this.getMandibleValue();
    this.getModelScan();
    if (this.orderService.buttons.fullGuideCase) {
      this.conversion.controls.software.patchValue(3);
      this.conversion.controls.format.patchValue(8);
    };
    this.checkRestoration();
  }

  ngDoCheck(): void {
    this.checkMaxillaAndMandible();
    this.mandibleCheck = this.orderService.teethImplantSites.mandible.length > 0;
    this.maxillaCheck = this.orderService.teethImplantSites.maxilla.length > 0;
    this.checkRestoration();


  }

  checkRestoration() {
    if (this.orderService.buttons.restorations) {
      this.maxillaAndMandible = false;
    }
  }

  checkMaxillaAndMandible() {
    if (!this.maxillaAndMandible) {
      this.conversion.controls.maxilla.setValue(20);
      this.conversion.controls.mandible.setValue(20);
    };
    if (this.orderService.buttons.surgicalGuide || this.orderService.buttons.treatmentPlan) {
      this.maxillaAndMandible = false;
    }
    else {
      this.maxillaAndMandible = true;
    }
  };

  /**
   * This function get software input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getSoftwareValue(): void {
    this.conversion.controls.software.valueChanges.subscribe((software: number) => {
      this.software = software;

    });
  };

  checkFormat(event: MatRadioChange): void {
    if (event.value === 3) {
      this.conversion.patchValue({ format: 8 });
    }
    else if (event.value === 6)
      this.conversion.patchValue({ format: 13 })
    else {
      this.conversion.patchValue({ format: 1 });
    }
    this.conversion.get('format')?.updateValueAndValidity();
    if (this.orderService.buttons.surgicalGuide) {
      this.surgicalGuide?.controls?.software.patchValue(event.value);
      this.surgicalGuide?.controls?.typeofGuide.patchValue(1);
      this.surgicalGuide?.controls?.guidedSurgeryKitType.patchValue(null);
      this.surgicalGuide?.controls?.guided.patchValue(1);
      this.surgicalGuide?.controls?.intraoralScan.patchValue(1);
    }
    this.changeValue.emit();
  }

  /**
   * This function get maxilla input value
   * Adding or removing typeMaxilla control depend on maxilla input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getMaxillaValue(): void {
    if (this.conversion.contains('maxilla')) {
      this.conversion.controls.maxilla.valueChanges.subscribe((maxilla: number) => {
        this.maxilla = maxilla;
      });
    };
  };

  /**
   * This function get mandible input value
   * Adding or removing typeMandible control depend on mandible input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getMandibleValue(): void {
    if (this.conversion.contains('mandible')) {
      this.conversion.controls.mandible.valueChanges.subscribe((mandible: number) => {
        this.mandible = mandible;
      });
    }
  };

  /**
   * This function get modelScan input value
   * Adding or removing oral control depend on mandible input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getModelScan(): void {
    this.conversion.controls.modelScan.valueChanges.subscribe((modelScan: number) => {
      this.modelScan = modelScan;
      if (this.modelScan === 1) {
        this.conversion.addControl('oral', this.fb.control(1, Validators.required));
        this.getOralValue();
      } else {
        this.conversion.removeControl('oral');
      }
    });
  };

  /**
   * This function get oral input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getOralValue(): void {
    this.conversion.controls.oral.valueChanges.subscribe((oral: number) => {
      this.oral = oral;
    });
  };

}
