import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {IchangePassword} from 'shared/shared-interfaces/change-password.model';
import {MustMatch} from './../../../shared-functions/MustMatch';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';
import {PasswordValidators} from '../../../shared-validators/password-validator/password.validator';

/**
 * Copyright (c) 3ddx
 *
 * @summary Password Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-password-form',
  templateUrl: './password-form.component.html',
  styleUrls: ['./password-form.component.css']
})
export class PasswordFormComponent implements OnInit {
  changePasswordForm!: FormGroup;
  changePassData!: IchangePassword;
  confirmHide: boolean = true;
  newHide: boolean = true;
  hide: boolean = true;
  submitted: boolean = false;
  @Output() changePassOBJ: EventEmitter<IchangePassword> = new EventEmitter();
  form = this.fb.group({
    verby: [{value: '', disabled: true}]
  });
  htmlID = CStaticIds;
  constructor(private fb: FormBuilder) {
  }

  ngOnInit(): void {
    this.creatChangePasswordForm();
  }

  /**
   * This function creat change password form group
   * And matching between password and confirm password
   * @return void
   */
  creatChangePasswordForm(): void {
    this.changePasswordForm = this.fb.group({
        password: ['', [Validators.required]],
        newPassword: ['', [Validators.required,
          Validators.minLength(8),
          PasswordValidators.hasDigit(),
          PasswordValidators.hasUpper(),
          PasswordValidators.hasLower(),
          PasswordValidators.hasSpecialChar()]],
        confirmPassword: ['', [Validators.required],
        ]
      },
      {
        validator: MustMatch('newPassword', 'confirmPassword')
      }
    );
  };

  /**
   * This function submit form and output data to password component
   * @return void
   */
  submit() {
    this.submitted = true;
    this.changePassData = {
      oldPassword: this.changePasswordForm.value.password,
      newPassword: this.changePasswordForm.value.newPassword
    };
    this.changePassOBJ.emit(this.changePassData);

  }

}
