import {
  AfterViewInit,
  Component,
  EventEmitter,
  HostListener,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { SendToFriendComponent } from '../../send-to-friend/send-to-friend/send-to-friend.component';
import { ActivatedRoute, Router } from '@angular/router';
import { IOrderListData } from '../../../../../projects/connect/src/app/Models/Interfaces/Order/OrderList/OrderListData';
import { IAddToFriend } from '../../../../shared-interfaces/add-to-friend';
import { GenerateOrderDataService } from '../../../../shared-services/generate-order-data.service';
import { AdvancedSearchComponent } from '../../../advanced-search/advanced-search.component';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';
import { OrderListService } from 'projects/connect/src/app/Services/OrderServices/OrderListService/order-list.service';
import { SubOrderType } from 'shared/shared-static-data/SubOrderType';
import { VoucherComponent } from './../../../shard-popup/voucher/voucher.component';
import { DeductedCostComponent } from '../../../shard-popup/deducted-cost/deducted-cost.component';
import { OrderStatusAction } from 'shared/shared-enums/order-status-action';
import { ActionsService } from '../../../../../projects/connect/src/app/Services/OrderServices/actions/actions.service';
import { UploadFileComponent } from '../../../upload-file/upload-file.component';
import { UploadGFMRFilesComponent } from '../../../upload-gfmrfiles/upload-gfmrfiles.component';
import { AcceptRejectRestorationGfmrComponent } from '../../../accept-reject-restoration-gfmr/accept-reject-restoration-gfmr.component';
import { TrackShippedOrderComponent } from '../../../track-shipped-order/track-shipped-order.component';
import { PrepareTrackingNumberComponent } from '../../../prepare-tracking-number/prepare-tracking-number.component';
import { PreparePackageComponent } from '../../../prepare-package/prepare-package.component';
import { LazyLoadEvent } from 'primeng/api';
import { Paginator } from 'primeng/paginator';

@Component({
  selector: 'app-view-orders',
  templateUrl: './view-orders.component.html',
  styleUrls: ['./view-orders.component.css']
})
export class ViewOrdersComponent implements OnInit, OnChanges, AfterViewInit {
  @HostListener('scroll', ['$event'])
  @Input() logoSrc: string | undefined;
  @Input() showShorMoreBtn: boolean = true;
  @Input() orderList: IOrderListData[] = [];
  @Input() Voucher: any;
  @Input() ordersCount: any;
  @Input() patients: any;
  @Output() orderDetails: EventEmitter<any> = new EventEmitter();
  @Output() paginationOrder: EventEmitter<any> = new EventEmitter();
  @Output() addToFriend: EventEmitter<any> = new EventEmitter();
  @Output() addToOrderPage: EventEmitter<any> = new EventEmitter();
  @Output() reload: EventEmitter<any> = new EventEmitter();
  @Output() filterSearch: EventEmitter<any> = new EventEmitter();
  @Output() onDownloadFile: EventEmitter<any> = new EventEmitter();
  @Output() onUploadGRMRFilesEmmit: EventEmitter<any> = new EventEmitter();
  @Output() onConfirmingSurgicalGuide: EventEmitter<any> = new EventEmitter();
  @Output() onUploadFileEmmit: EventEmitter<any> = new EventEmitter();
  @Output() onAcceptRejectRestorationGFMREmmit: EventEmitter<any> = new EventEmitter();
  @Output() onPrepareTrackingNumberEmmit: EventEmitter<any> = new EventEmitter();
  @Output() patientNameSearch: EventEmitter<any> = new EventEmitter();
  @Output() onChangePatientName: EventEmitter<any> = new EventEmitter();
  action = OrderStatusAction;
  orderArr: any[] = [];
  paginatorView!: boolean;
  htmlID = CStaticIds;
  searchResult: any;
  orderNumber = 21;
  dialogUploadFileRef!: MatDialogRef<UploadFileComponent, any>;
  dialogUploadGFMRFileRef!: MatDialogRef<UploadGFMRFilesComponent, any>;
  dialogAcceptRejectRestorationGFMRRef!: MatDialogRef<AcceptRejectRestorationGfmrComponent, any>;
  dialogTrackShippedOrder!: MatDialogRef<TrackShippedOrderComponent, any>;
  dialogPrepareTrackingRef!: MatDialogRef<PrepareTrackingNumberComponent, any>;
  dialogRef!: MatDialogRef<PreparePackageComponent, any>;
  // Specifies whether the value is digital or not
  isDigital:boolean=true;
  /**
   * The Requirements For The Construction Of The Component
   * @param router For The routing
   * @param dialog For The dialogue
   * @param generateOrderDataService For The generateOrderDataService
   * @param orderListService
   * @param actionsService
   */
  constructor(
    public dialog: MatDialog,
    private router: Router,
    private generateOrderDataService: GenerateOrderDataService,
    public orderListService: OrderListService,
    private actionsService: ActionsService,
    private route: ActivatedRoute
  ) {

  }

  ngOnChanges(changes: SimpleChanges): void {
    this.orderArr = this.generateOrderDataService.onGenerateArr(changes.orderList?.currentValue);
  }

  ngAfterViewInit(): void {
  window.scrollTo(0, this.orderListService.pageOffset);
}

  ngOnInit(): void {
  }

  /**
   * open send order to friend pop-up
   * @return void
   */
  sendToFriend(order: any): void {
    const dialogRef = this.dialog.open(SendToFriendComponent, {
      minWidth: '50vw',
      maxWidth: '70vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        order
      }
    });
    dialogRef.afterClosed().subscribe((result: IAddToFriend) => {
      if (result) {
        result.orderId = order.orderId;
        this.addToFriend.emit(result);
      }
    });
  }

  /**
   * navigate to view order details
   * @param id order id
   * @return void
   */
  viewOrder(id: number): void {
    this.orderDetails.emit(id);
    this.orderListService.showMore = true;
    this.orderListService.pageOffset = window.pageYOffset;
  }
  /**
   * reload orders
   * @return void
   */
  onReload(): void {
    this.reload.emit(null);
    this.orderListService.pageOffset = 0;
  }
  /**
   * add to order
   * @param orderId order id
   * @return void
   */
  addToOrder(orderId: number): void {
    this.addToOrderPage.emit(orderId);
    this.orderListService.pageOffset = window.pageYOffset;
  }

  loadOrders(event: LazyLoadEvent) {
     if (event.first !== undefined) {
       if (this.orderListService.showMore) {
         let pageNumber = localStorage.getItem('pageNumber');
         if (pageNumber) this.orderListService.pageNumber = +pageNumber;
       } else {
         this.orderListService.pageNumber = event.first / this.orderNumber;
         localStorage.setItem("pageNumber", String(this.orderListService.pageNumber));
       }
     }
     ;
     this.orderListService.showMore = false;
  }
  /**
   * get current page number to return to the same page of orders
   * @param $event first and row of the data view component
   * @return void
   */
  getCurrentPage($event: any): void {
    this.paginationOrder.emit();
    this.orderListService.firstOrders = $event.first;
    this.orderListService.pageOffset = 0;
  }

  voucherPopup(orderId: number, currency: string) {
    const dialogRef = this.dialog.open(VoucherComponent, {
      minWidth: '30vw',
      maxWidth: '50vw',
      maxHeight: '90vh',
      data: {
        Voucher: this.Voucher,
        voucherId: orderId,
        currency,
        logo: this.logoSrc
      }
    });
  }
  /**
   * order search
   * @return void
   */

  onOrderSearch(): void {
    const dialogRef = this.dialog.open(AdvancedSearchComponent, {
      minWidth: '30vw',
      maxWidth: '50vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderSummary: this.onGenerateOrderSummary(),
        subOrderTypes: this.onGenerateSubOrder()
      }
    });
    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        this.filterSearch.emit(result);
        this.orderListService.searchResult = {
          patientName: result.patientName,
          orderType: Object.values(SubOrderType).find(e => e.value == result.orderType)?.name!,
          doctorName: result.doctorName,
          dateStart: result.dateStart,
          dateEnd: result.dateEnd
        };
        this.orderListService.searchValue = '';
      }
    });
  }


  onGenerateOrderSummary(): string[] {
    const arr: string[] = [];
    this.orderArr.map(sub => sub.subOrders.map((state: { subOrderStatus: { status: string; }; }) => state.subOrderStatus.status)).forEach(ar => {
      ar.forEach((el: any) => {
        arr.push(el);
      });
    });
    return [...new Set(arr)];
  }
  /**
   * generate sub order
   * @return string[]
   */

  onGenerateSubOrder(): string[] {
    const arr: string[] = [];
    this.orderArr.map(sub => sub.subOrders.map((state: { subOrderStatus: { subOrderName: string; }; }) => state.subOrderStatus.subOrderName)).forEach(ar => {
      ar.forEach((el: any) => {
        arr.push(el);
      });
    });
    return [...new Set(arr)];
  }
  /**
   * view deducted details
   * @return void
   */

  viewDeductedDetails(order: any): void {
    const deductedSubOrders = order.subOrders.filter((subOrder: any) => subOrder.deductedCost);
    const dialogRef = this.dialog.open(DeductedCostComponent, {
      minWidth: '50vw',
      minHeight: '40vh',
      maxWidth: '80vw',
      maxHeight: '90vh',
      autoFocus: false,
      data: {
        logo: this.logoSrc,
        deductedSubOrders,
      }
    });
  }
  /**
   * open Upload Files pop-up and emit result data
   * @return void
   */
  onUploadFile(orderDetails: any, orderId: number): void {
    this.dialogUploadFileRef = this.dialog.open(UploadFileComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderId,
        scanType: orderDetails.subOrderStatus.uploadType,
      }
    });
    this.dialogUploadFileRef.afterClosed().subscribe(result => {
      if (result) {
        const data = {
          file: result.file,
          userId: Number(localStorage.getItem('userCNId')),
          orderId,
          token: localStorage.getItem('token'),
          scanType: orderDetails.subOrderStatus.uploadType,
          subOrderDetails: orderDetails.subOrderStatus
        };
        // @ts-ignore
        this.onUploadFileEmmit.emit({ data, orderId });
      }
    });
  }

  onDownloadEmmitFile(orderDetails: any, orderId: number): void {
    const data = {
      subOrderId: orderDetails.subOrderStatus.subOrderId,
      portable: false
    };
    this.onDownloadFile.emit({ data, orderId });
  }
  /**
   * Upload GFMRF Files pop-up and emit result data
   * @return void
   */
  onUploadGFMRFiles(orderDetails: any, orderId: number): void {
    this.dialogUploadGFMRFileRef = this.dialog.open(UploadGFMRFilesComponent, {
      maxWidth: '90vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderId
      }
    });
    this.dialogUploadGFMRFileRef.afterClosed().subscribe(result => {
      if (result) {
        const data = {
          lowerSTLFile: result.lowerSTLFile,
          upperSTLFile: result.upperSTLFile,
          biteSTLFile: result.biteSTLFile,
          userId: Number(localStorage.getItem('userCNId')),
          orderId,
          token: localStorage.getItem('token'),
          scanType: orderDetails.subOrderStatus.uploadType,
          subOrderDetails: orderDetails.subOrderStatus
        };
        this.onUploadGRMRFilesEmmit.emit({ data, orderId });
      }
    });
  }
  onConfirmSurgicalGuide(orderId: number, orderDetails: any): void {
    this.onConfirmingSurgicalGuide.emit({ orderDetails, orderId });
  }
  /**
   * Accept/Reject RestorationGFMR pop-up and emit result data
   * @return void
   */
  onAcceptRejectRestorationGFMR(orderId: number, orderDetails: any): void {
    this.dialogAcceptRejectRestorationGFMRRef = this.dialog.open(AcceptRejectRestorationGfmrComponent, {
      maxWidth: '40vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderDetail: orderDetails,
        orderId
      }
    });
    this.dialogAcceptRejectRestorationGFMRRef.afterClosed().subscribe(result => {
      if (result) {
        result.statusId = orderDetails.statusId;
        result.subOrderId = orderDetails.subOrderId;
        result.orderId = orderId;
        this.onAcceptRejectRestorationGFMREmmit.emit(result);
      }
    });
  }
  /**
   * Prepare Tracking Number Notes pop-up
   * @return void
   */
  onPrepareTrackingNumber(orderDetails: any, orderId: number): void {
    this.dialogPrepareTrackingRef = this.dialog.open(PrepareTrackingNumberComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc
      }
    });
    this.dialogPrepareTrackingRef.afterClosed().subscribe(result => {
      if (result) {
        result.subOrderName = orderDetails.subOrderStatus.subOrderName;
        this.onPrepareTrackingNumberEmmit.emit({ data: result, orderId });
      }
    });
  }
  trackSubOrder(orderDetails: any): void {
    this.dialogTrackShippedOrder = this.dialog.open(TrackShippedOrderComponent, {
      maxWidth: '60vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderDetails
      }
    });
  }
  /**
   * prepare package pop-up
   * @return void
   */
  onPreparePackage(orderDetails: any, orderId: number, patientName: string, scanningCenter: string): void {
    this.dialogRef = this.dialog.open(PreparePackageComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logoSrc,
        orderId,
        patientName,
        scanningCenter,
        subOrderName: orderDetails.subOrderStatus.subOrderName
      }
    });
  }
  /**
   * check orders actions
   * @return any
   */
  statusAction(actionNeeded: any, orderDetails: any, orderId: number, order?: any): any {
    switch (actionNeeded) {
      case this.action.uploadScan:
        if (!this.orderListService.uploadingId.has(JSON.stringify({id: orderId, type: orderDetails.subOrderStatus.uploadType}))) {
          if ((orderDetails.subOrderStatus.uploadType === SubOrderType.GFMR.uploadType)) {
            this.onUploadGFMRFiles(orderDetails, orderId);
          } else {
            this.onUploadFile(orderDetails, orderId);
          }
        }
        break;
      case this.action.Download:
        this.onDownloadEmmitFile(orderDetails, orderId);
        break;
      case this.action.Confirm:
        this.onConfirmSurgicalGuide(orderId, orderDetails);
        break;
      case this.action.Approval_Reject:
        this.onAcceptRejectRestorationGFMR(orderId, orderDetails);
        break;
      case this.action.ShippingRestoration:
        this.trackSubOrder(orderDetails);
        break;
      case this.action.InProduction:
        this.onConfirmSurgicalGuide(orderId, orderDetails);
        break;
      case 'preparePackage':
        this.onPreparePackage(orderDetails, orderId, order.patientName, order.scanningCenter);
        break;
      case 'trackNumber':
        this.onPrepareTrackingNumber(orderDetails, orderId);
        break;
      case 'surgicalShipped':
        this.onConfirmSurgicalGuide(orderId, orderDetails);
        break;
      case 'surgicalTrack':
        this.trackSubOrder(orderDetails);
        break;
    }
  }
  /**
   * check current lay out view
   * @return void
   */
  getCurrentLayOut($event: any): void {
    this.orderListService.layOut = $event.layout;
  }
  /**
   * emit patient name search
   * @return void
   */
  patientNameSearchEmitter(): void {
    this.patientNameSearch.emit(null);
  }
  /**
   * emit patient name change
   * @return void
   */
  onChangePatientNameEmitter($event: any): void{
    this.onChangePatientName.emit($event);
  }

  /**
   * Filters the patient names based on the search value.
   * If the search value is empty or not provided, it returns the first 300 patients.
   * Otherwise, it filters the patients based on the search value and returns the first 300 matching patients.
   *
   * @returns An array of patients.
   */
  filterPatientNames(): any[] {
    /**
     * removes whitespaces from a given string
     * @param str sentence to remove whitespaces from
     * @returns the string without any whitespaces
     */
    const removeWhitespace = (str:string) => str.replace(/\s+/g, '')

    if (
      !this.orderListService.searchValue ||
      removeWhitespace(this.orderListService.searchValue) == ''
    ) {
      // Return the first 600 patients when no search value is provided
      return this.patients.slice(0, 600);
    } else {
      // Filter the patients based on the search value and return the first 600 matches
      return this.patients
        .filter((patient: any) =>
        removeWhitespace(
          patient.patientName
            .toLowerCase()
        ).includes(removeWhitespace(this.orderListService.searchValue.toLowerCase()))
        )
        .slice(0, 600);
    }
  }
}
