import {Injectable} from '@angular/core';
import {DataService} from '../../DataService/data.service';
import {Observable} from 'rxjs';
import {APIName} from '../../../../../../../shared/API/APIName';
import {IApiData} from '../../../Models/Interfaces/APIs/apiData.model';
import {IAcceptRejectRestorationGFMR} from '../../../Models/Interfaces/Order/AcceptRejectRestorationGFMR/accept-reject-restoration-gfmr';

@Injectable({
  providedIn: 'root'
})
export class AcceptRejectRestorationService {
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
  constructor(private dataService: DataService) {
  }
  /**
   * send feedback
   * @param accRejOBJ Accept/Reject RestorationGFMR object
   * @return Observable<any>
   */
  onAcceptRejectRestorationGFMR(accRejOBJ: IApiData<IAcceptRejectRestorationGFMR>): Observable<any> {
    return this.dataService.post(APIName.orderDetailsActions.acceptRejectRestorationGFMR, accRejOBJ);
  }
}
