import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ServiceNameEnum } from 'shared/shared-enums/ServiceNameEnum';
import { VoucherService } from 'shared/shared-services/Voucher/voucher.service';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-voucher',
  templateUrl: './voucher.component.html',
  styleUrls: ['./voucher.component.css']
})
export class VoucherComponent implements OnInit {
  voucher: any;
  displayedColumns: string[] = ['Order ID', 'Order Type', 'Patient Name', 'Amount deducted', 'Remaining'];
  htmlID = CStaticIds;
  constructor(
    private voucherService: VoucherService,
    public dialogRef: MatDialogRef<VoucherComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void {
    this.data.Voucher.VoucherReq.services = [
      {
        voucherId: this.data.voucherId,
        ConditionsAnded: false,
        columns: [],
        serviceName: ServiceNameEnum.VIEW_VOUCHER_HISTORY
      }
    ];
    this.getVoucherById();
  }

  getVoucherById() {
    this.voucherService.getInfoVoucher(this.data).subscribe(voucher => {
      this.voucher = voucher;
    });
  };

  close() {
    this.dialogRef.close();
  }

}
