import {Injectable} from '@angular/core';
import {Subscription} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UploadProgressService {
  secondsElapsed: any;
  bytesPerSecond: any;
  remainingBytes: any;
  secondsRemaining!: any;
  startedAt: Date = new Date();

  constructor() {
  }

  returnProgress(loaded: number, total: number): number {
    return Math.round(loaded / total * 100);
  }

  calculateTimeRemain(loaded: number, fileSize: number): any {
    this.secondsElapsed = (new Date().getTime() - this.startedAt.getTime()) / 1000;
    this.bytesPerSecond = this.secondsElapsed ? loaded / this.secondsElapsed : 0;
    this.remainingBytes = fileSize - loaded;
    if (this.remainingBytes / this.bytesPerSecond > 60) {
      this.secondsRemaining = this.secondsElapsed ? Math.floor(Math.round(this.remainingBytes / this.bytesPerSecond) / 60) + ' Minutes' : 'Calculating';
    } else {
      if (Math.round(this.remainingBytes / this.bytesPerSecond) < 0) {
        this.secondsRemaining = '0 Seconds';
      } else {
        this.secondsRemaining = this.secondsElapsed ? Math.round(this.remainingBytes / this.bytesPerSecond) + ' Seconds' : 'Calculating';
      }
    }
    return this.secondsRemaining;
  }


}
