  convertBlobToFile(blob: Blob, fileName: string): File {
    const fileOptions = {
      type: blob.type,
      lastModified: new Date().getTime()
    };

    return new File([blob], fileName, fileOptions);
  }

  async convertResponseToFile(response: any, fileName: string): Promise<File> {
    console.log(response);

    // const blob = await response.blob();
    return this.convertBlobToFile(response, fileName);
  }
