
 import {Injectable} from '@angular/core';
 import {IApiData} from '../../../projects/connect/src/app/Models/Interfaces/APIs/apiData.model';
 import {IAddDoctor} from '../../../projects/connect/src/app/Models/Interfaces/AddDoctor/Adddoctor';
 import {IMainResponse} from '../../../projects/connect/src/app/Models/Interfaces/Main/MainResponse';
 import {Observable} from 'rxjs';
 import {APIName} from '../../API/APIName';
import { HttpClient } from '@angular/common/http';

 @Injectable({
   providedIn: 'root'
 })
 export class addDoctorService {
   /**
    * The Requirements For The Construction Of The service
    * @param httpClient for managing Http methods
    */
   constructor(private http: HttpClient) {
   }
   /**
    * send addDoctor
    * @param adddoctorOBJ addDoctor object
    * @return Observable
    */
   sendaddDoctor(adddoctorOBJ: IApiData<IAddDoctor>): Observable<any> {
     return this.http.post(APIName.adddoctor, adddoctorOBJ);
   }
 }
