import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-send-stone-model',
  templateUrl: './send-stone-model.component.html',
  styleUrls: ['./send-stone-model.component.css']
})
export class SendStoneModelComponent implements OnInit {
  logo = 'assets/Images/connectLogo.png';
  /**
   * The Requirements For The Construction Of The Component
   * @param {MatDialogRef<SendStoneModelComponent>} dialogRef For The Dialogue Reference
   * @param {any} data For The Data Inherited From The Parent Component
   */
  constructor(public dialogRef: MatDialogRef<SendStoneModelComponent>,
              @Inject(MAT_DIALOG_DATA) public data: any, ) { }

  ngOnInit(): void {
  }
  onSubmit(): void {
    this.dialogRef.close();
  }
}
