import { Injectable } from '@angular/core';
import { ITeethChart } from 'shared/shared-interfaces/teeth-chart';

@Injectable({
    providedIn: 'root'
})

export class OrderSharedService {
    buttons = {
        fullGuideCase: false,
        conversion: false,
        treatmentPlan: false,
        surgicalGuide: false,
        restorations: false,
        radiologyReport: false,
        teethChart: false,
        otherServices:false
    };
    surgicalChecked = false;
    teethChart: ITeethChart = {
        crownSites: [],
        abutmentSites: [],
        implantSites: [],
        teethToBeExtracted: [],
        missingTeeth: []
    } as ITeethChart;
    teethImplantSites: any = {
        maxilla: [],
        mandible: []
    }
  checkMaxDOB = false;
  checkMinDOB = false;
  checkMinDOI = false;
    constructor() { }
    getImplantsArches(): any {
        this.teethImplantSites.maxilla = this.teethChart.implantSites.filter((implant: number) => implant <= 16);
        this.teethImplantSites.mandible = this.teethChart.implantSites.filter((implant: number) => implant > 16);
    }
}
