import {Billing} from 'shared/shared-interfaces/billing';
import {ArchType} from 'shared/shared-static-data/ArchType';
import {CurrencyTypes} from 'shared/shared-static-data/CurrencyTypes';
import {SubOrderType} from 'shared/shared-static-data/SubOrderType';


export const handleOrdersBilling = (Billing: Billing[]): Array<Billing> => {
  Billing.map((response: Billing, index: number): void => {
    Billing[index].totalCost = 0;
    Billing[index].totalCost += +response.subOrderCost;
    Billing[index].totalCost += +response.subOrderCostDiff;
    Object.values(SubOrderType).map((res): void => {
      if (res.value === response.subOrderEnumId) {
        Billing[index].subOrderName = res.name;
      }
    });
    Object.values(ArchType).map((res): void => {
      if (res.value === response.mandibleEnumId) {
        Billing[index].mandibleName = res.name;
      }
      if (res.value === response.maxillaEnumId) {
        Billing[index].maxillaName = res.name;
      }
    });
    Object.values(CurrencyTypes).map((res): void => {
      if (res.value === +response.currencyEnumId) {
        Billing[index].currencyName = res.symbol;
      }
    });
    response.subOrderCost ? Billing[index].cost += response.subOrderCost : Billing[index].cost = 0;
    response.subOrderCostDiff ? Billing[index].cost += response.subOrderCostDiff : Billing[index].cost = 0;
  });
  return Billing;
}
