import {Component, Inject, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {TrackingNumberProviders} from '../../shared-static-data/TrackingNumberProviders';
import {CStaticIds} from '../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-prepare-tracking-number',
  templateUrl: './prepare-tracking-number.component.html',
  styleUrls: ['./prepare-tracking-number.component.css']
})
export class PrepareTrackingNumberComponent implements OnInit {
  prepareTrackingNumberForm!: FormGroup;
  serviceProvidersList = Object.values(TrackingNumberProviders).filter(c => c !== TrackingNumberProviders.NONE);
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param formBuilder for using FormBuilder
   * @param data for dialog data
   */
  constructor(
    public formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<PrepareTrackingNumberComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.prepareTrackingNumberForm = this.formBuilder.group({
      serviceProvider: [null, [Validators.required]],
      trackingNumber: [null, [Validators.required]],
    });
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * get form controls
   * @return void
   */
  get errorControl(): any {
    return this.prepareTrackingNumberForm.controls;
  }
  /**
   * onSubmit check form validation
   * @return void
   */
  onSubmit(): void {
    if (!this.prepareTrackingNumberForm.valid) {
      return;
    }
    this.dialogRef.close(this.prepareTrackingNumberForm.value);
  }
}
