import { Injectable } from '@angular/core';
import { ILogin } from '../../../Models/Interfaces/Auth/Login';
import { DataService } from '../../DataService/data.service';
import { Observable } from 'rxjs';
import { APIName } from '../../../../../../../shared/API/APIName';
import { IMainResponse } from '../../../Models/Interfaces/Main/MainResponse';
import { IApiData } from '../../../Models/Interfaces/APIs/apiData.model';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class LoginService {
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   * @param router for routing
   */
  constructor(private dataService: DataService, public router: Router) {
  }
  /**
   * log in
   * @param loginObj logIn object
   * @return Observable<IMainResponse>
   */
  login(loginObj: IApiData<ILogin>): Observable<IMainResponse> {
    return this.dataService.post(APIName.login, loginObj);
  }
  /**
   * log out
   * @return void
   */
  logout(): void {
    this.router.navigate(['auth', 'sign-in']);
    const myLanguage = localStorage.getItem('language') || '';
    localStorage.clear();
    localStorage.setItem('language', myLanguage);
  }
}
