import { Component, Input, OnInit, DoCheck } from '@angular/core';
import { OrderCost } from 'projects/connect/src/app/Models/Interfaces/Order/OrderCostResponse.model';
import { OrderSharedService } from '../../../shared-services/order-shared.service';
import { CStaticIds } from '../../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-select-orders',
  templateUrl: './select-orders.component.html',
  styleUrls: ['./select-orders.component.css']
})
export class SelectOrdersComponent implements OnInit, DoCheck {
  @Input() orderCost!: OrderCost;
  @Input() isGFMRBoth!: boolean;
  @Input() isModelScanConv!: boolean;
  @Input() restype!: Number;
  @Input() isModelScanSG!: boolean;
  /** to track if we're adding to order has laser scan then hide model scan price */
  @Input() hasLaserScan:boolean = false;

  htmlID = CStaticIds;
  maxilla!: number;
  mandible!: number;
  constructor(public orderService: OrderSharedService) {
  }

  ngOnInit(): void {
  }

  ngDoCheck(): void {
    this.mandible = this.orderService.teethChart.implantSites.filter(res => res > 16).length;
    this.maxilla = this.orderService.teethChart.implantSites.filter(res => res <= 16).length;
  }


  getRestcost() {
    if (this.restype == 1) {
      return this.orderCost.rushCostRes.cost + this.orderCost.pouringCostRes.cost + this.orderCost.abutmentsCost.cost + this.orderCost.crownsCost.cost
    } else if (this.restype == 2) {
      if (this.orderService.teethChart.implantSites.length === 0){
        return 0 ;
      }
      else {
        return this.orderCost.archOfIntersetCostRes.cost + this.orderCost.chairSideAssCostRes.cost;
      }
    }
     return 0 ;

  }

}
