import { Component } from '@angular/core';
import { ERequestTypes } from 'projects/connect/src/app/Models/Enums/RequestTypes/RequestTypes';
import { LoginService } from 'projects/connect/src/app/Services/AuthServices/LoginService/login.service';
import { LocalStorageService } from 'projects/connect/src/app/Services/LocalStorageService/local-storage.service';
import { ResponseCodeEnum } from 'shared/shared-enums/ResponseCodeEnum';
import { environment } from '../environments/environment';

declare var ipcRenderer: any;
declare var remote: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  loginObj = { requestType: ERequestTypes.ADD, services: [{}] };
  patientName!: string;
  filePath!: string;

  constructor(
    private loginService: LoginService,
    private localStorageService: LocalStorageService
  ) {}

  async ngOnInit() {
    

    const { emailAddress, password, name, filePath } = JSON.parse(localStorage.getItem('obj')!);
    this.patientName = name;
    this.filePath = filePath;
    if(this.filePath){
      this.test();
    }
    this.loginObj.services[0] = {
      userName: emailAddress,
      password: password,
      serviceName: '',
      columns: [],
      ConditionsAnded: false,
    };
    // @ts-expect-error
    this.loginService.login(this.loginObj).subscribe((res) => {
      if (res.responsCode === ResponseCodeEnum.OK) {
        const responseOBJ = JSON.parse(res.responsString);
        if (!responseOBJ.needChange) {
          this.localStorageService.setLocalStorageGDBR(responseOBJ);
        }
      }
    });
  }

  test(){

  }

  back(){
    localStorage.back = true;
    ipcRenderer.send('back');
  }

  done(){
    ipcRenderer.send('done');
  }
}
