import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
	selector: 'app-errors-teeth-chart',
	templateUrl: './errors-teeth-chart.component.html',
	styleUrls: ['./errors-teeth-chart.component.css']
})
export class ErrorsTeethChartComponent implements OnInit {

	constructor(
		public dialogRef: MatDialogRef<ErrorsTeethChartComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any
	) { }

	ngOnInit(): void {
		this.data = this.data.teethErrors;
	}

}
