import {Injectable} from '@angular/core';
import {DatePipe} from '@angular/common';
import * as moment from 'moment-timezone';

@Injectable({
  providedIn: 'root'
})
export class DateService {

  constructor(public datePipe: DatePipe) {
  }

  dateToEST(date: number): string | null {
    return moment.tz(new Date(date * 1000), 'America/New_York').format('ddd MMM D YY hh:mm A') + ' EST';
  }

  dateToTimeStamp(date: Date): number {
    return new Date(date).getTime();
  }
  timeStampToEST(date: string, time?: string): any {
    if (date && date.length > 0) {
      let dateTimeStr = date;
      let timeStr = 'T00:00:00';
      if (time && time.length > 0) {
        timeStr = `T${time}`;
      }
      dateTimeStr += timeStr;
      const surgeryTime = moment.tz(dateTimeStr, 'America/New_York');
      return new Date(surgeryTime.format());
    }
  };
  formatToEST(date: any): string{
  return moment.tz( date, 'America/New_York').format('ddd MMM D YY hh:mm A') + ' EST';
  }
}
