import { Component, OnInit , Inject} from '@angular/core';
import { CStaticIds } from 'shared/shared-static-id/shared-static-id';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';


@Component({
  selector: 'app-add-doctor',
  templateUrl: './add-doctor.component.html',
  styleUrls: ['./add-doctor.component.css']
})
export class AddDoctorComponent implements OnInit {
  htmlID = CStaticIds;
  addDoctorForm!: FormGroup;
  isSubmitted = false;
  constructor(
    public dialogRef: MatDialogRef<AddDoctorComponent>,
    public formBuilder: FormBuilder,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) { }

  ngOnInit(): void {
    this.addDoctorForm = this.formBuilder.group({
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      email: ['', [Validators.required]],
      phone: ['', [Validators.required]],
    });
  }

  onSubmit(){
    this.isSubmitted = true;
    if (!this.addDoctorForm.valid) {
      return;
    } else {
      this.isSubmitted = false;
      this.dialogRef.close(this.addDoctorForm.value);
      this.addDoctorForm.value.firstName
    }

  }

  cancel(){
    this.dialogRef.close();
  }

  get errorControl(): any {
    return this.addDoctorForm.controls;
  }
}
