import {ViewportScroller} from '@angular/common';
import {Component, Input, OnDestroy, OnInit} from '@angular/core';
import {MatDialog} from '@angular/material/dialog';
import {Billing} from 'shared/shared-interfaces/billing';
import {PrintReceiptComponent} from '../print-receipt/print-receipt.component';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';
import {DateService} from '../../../shared-services/date.service';

/**
 * Copyright (c) 3ddx
 *
 * @summary Not Charged Table Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-not-charged-table',
  templateUrl: './not-charged-table.component.html',
  styleUrls: ['./not-charged-table.component.css']
})
export class NotChargedTableComponent implements OnInit, OnDestroy {
  @Input() notChargedBilling: Billing[] = [];
  htmlID = CStaticIds;

  constructor(
    public dialog: MatDialog,
    private scroller: ViewportScroller,
    public dateService: DateService
  ) {
  }

  ngOnInit(): void {
  }

  /**
   * This function to open dialog depend on subOrderEnumId and orderId(Print Receipt Component)
   * @param {string} orderId
   * @param {string} subOrderEnumId
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  openDialog(orderId: string, subOrderEnumId: string): void {
    this.dialog.open(PrintReceiptComponent, {
      autoFocus: false,
      minWidth: '50vw',
      maxWidth: '80vw',
      maxHeight: '90vh',
      data: {orderId, subOrderEnumId}
    });
  }

  scrollTop(): void {
    this.scroller.scrollToAnchor('table');
  }

  ngOnDestroy(): void {
    this.notChargedBilling = [];
  }
}
