import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {CStaticIds} from '../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-forget-password',
  templateUrl: './forget-password.component.html',
  styleUrls: ['./forget-password.component.css']
})
export class ForgetPasswordComponent implements OnInit {
  forgetPasswordForm!: FormGroup;
  isSubmitted = false;
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param formBuilder for using FormBuilder
   * @param data for dialog data
   */
  constructor(
    public formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<ForgetPasswordComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.forgetPasswordForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.email]],
    });
  }
  /**
   * get form controls
   * @return any
   */
  get errorControl(): any {
    return this.forgetPasswordForm.controls;
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * onSubmit check form validation
   * @return void
   */
  onSubmit(): void {
    this.isSubmitted = true;
    if (!this.forgetPasswordForm.valid) {
      return;
    } else {
      this.isSubmitted = false;
      this.dialogRef.close(this.forgetPasswordForm.value);
    }
  }
}
