import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ERequestTypes } from 'projects/connect/src/app/Models/Enums/RequestTypes/RequestTypes';
import { IChargeOn } from 'projects/connect/src/app/Models/Interfaces/Billing/ChargeOn/ChargeOn';
import { ChargeOnService } from 'projects/connect/src/app/Services/BillingServices/ChargeOnService/charge-on-service.service';
import { Subscription } from 'rxjs';
import { ResponseCodeEnum } from 'shared/shared-enums/ResponseCodeEnum';
import { ServiceNameEnum } from 'shared/shared-enums/ServiceNameEnum';
import { handleOrdersBilling } from 'shared/shared-functions/HandleOrdersBilling';
import { CurrencyTypes } from 'shared/shared-static-data/CurrencyTypes';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';
import {DateService} from '../../../shared-services/date.service';

@Component({
	selector: 'app-charge-on',
	templateUrl: './charge-on.component.html',
	styleUrls: ['./charge-on.component.css']
})
export class ChargeOnComponent implements OnInit, OnDestroy {
	logo = 'assets/Images/connectLogo.png';
	objData: IChargeOn = {} as IChargeOn;
	subscription!: Subscription;
	keyBilling!: number;
	currentDate: Date = new Date();
	billing: any;
  htmlID = CStaticIds;
	constructor(
		public dialogRef: MatDialogRef<ChargeOnComponent>,
		@Inject(MAT_DIALOG_DATA) public data: any,
		private service: ChargeOnService,
  public dateService: DateService
	) { }

	ngOnInit(): void {
		this.getChargeOn();
	}

	getChargeOn(): void {
		this.objData = {
			ConditionsAnded: false,
			chargedTime: this.data,
			columns: [],
			serviceName: ServiceNameEnum.CONNECT_BILLING_CHARGED_RECEIPT
		};
		this.subscription = this.service.getChargeOn({ services: [this.objData], requestType: ERequestTypes.VIEW }).subscribe(res => {
			if (res.responsCode === ResponseCodeEnum.OK) {
				this.keyBilling = +Object.keys(res.reponses[0].data[0]);
				this.billing = res.reponses[0].data[0][this.keyBilling];
				this.billing = handleOrdersBilling(this.billing);
				this.billing.name = localStorage.getItem('fName') + ' ' + localStorage.getItem('lName');
			}
		});
	}


	ngOnDestroy(): void {
		this.subscription.unsubscribe();
		this.objData = {} as IChargeOn;
	}
}
