import {Injectable} from '@angular/core';
import {DataService} from '../../DataService/data.service';
import {IApiData} from '../../../Models/Interfaces/APIs/apiData.model';
import {Observable} from 'rxjs';
import {IMainResponse} from '../../../Models/Interfaces/Main/MainResponse';
import {APIName} from '../../../../../../../shared/API/APIName';
import {IOrderTrackNumber} from '../../../Models/Interfaces/Order/OrderById/order-track-number';
import {IOrderDownload} from '../../../Models/Interfaces/Order/OrderById/order-download';

@Injectable({
  providedIn: 'root'
})
export class OrderDetailsService {
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
  constructor(private dataService: DataService) {
  }
  /**
   * send order tracking number
   * @param trackOBJ tracking object
   * @return Observable<IMainResponse>
   */
  sendOrderTrackingNumber(trackOBJ: IApiData<IOrderTrackNumber>): Observable<IMainResponse> {
    return this.dataService.post(APIName.orderDetailsActions.trackNumber, trackOBJ);
  }
  /**
   * download files
   * @param downloadObj download object
   * @return Observable<any>
   */
  onDownloadFile(downloadObj: IApiData<IOrderDownload>): Observable<any> {
    return this.dataService.postDownload(APIName.orderDetailsActions.downloadFile, downloadObj);
  }
}
