import {  ChangeDetectorRef, Component, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { ToastrService } from 'shared/shared-services/toastr.service';
import { AddOrderFormComponent } from '../add-order-form.component';
import { addDoctorService } from 'shared/shared-services/doctor/add-doctor.service';
import { StepperSelectionEvent } from '@angular/cdk/stepper';
import { MatStep, MatStepper } from '@angular/material/stepper';

type View = 'start' | 'teeth' | 'stepper' | 'uploader';

type Uploads = 'stl' | 'photos' | 'additional';

@Component({
  selector: 'app-add-order-form-electron',
  templateUrl: './add-order-form-electron.component.html',
  styleUrls: ['./add-order-form-electron.component.css'],
})
export class AddOrderFormElectronComponent extends AddOrderFormComponent {
  @Input() patientName!:string;
  @Input() filePath!:string;

  @Output() back = new EventEmitter();

  
  @ViewChild('stepper') stepper!: MatStepper;

  currentView: View = 'start';
  isStepperLastIndex = false;
  lastStepLabel!: string;

  uploadErrMsg = {
    stl: '',
    photos: '',
    additional: '',
  };
  uploadFiles: any = {};

  lastStep:any;


  constructor(
    public fb: FormBuilder,
    public orderService: OrderSharedService,
    public dialog: MatDialog,
    public addDoctorService: addDoctorService,
    public route: ActivatedRoute,
    public toastService: ToastrService,
    public readonly changeDetectorRef: ChangeDetectorRef
  ) {
    super(fb, orderService, dialog, addDoctorService, route, toastService,changeDetectorRef);
  }

  createBasicForm(): void {
    this.basicInfoFormGroup = this.fb.group({
      doctor: '',
      billing: 0,
      gender: 2,
      patientName: [this.patientName, Validators.required],
    });
  };


  checkSelectedOrders() {
    return Object.values(this.orderService.buttons).some((x) => x);
  }

  showTeeth() {
    console.log(this.restorationForm);
    
    return (
      (
        (
          this.orderService.buttons.restorations &&
            this.restorationForm.controls.restType.value==2 
          ) ||
        this.orderService.buttons.surgicalGuide ||
        this.orderService.buttons.treatmentPlan) &&
      !this.checkOwnCopyTP()
    );
  }

  changeView(view: View) {
    if (view === 'teeth') {
      if (this.showTeeth()) {
        this.currentView = view;
      } else {
        this.currentView = this.currentView === 'start' ? 'stepper' : 'start';
      }
    } else {
      this.currentView = view;
    }

    // if(view === 'stepper'){
    //   setTimeout(() => {
    //     console.log('change ', this.stepper);
      
    //   this.stepper.selectedIndex =2;
    //   }, 300);
    // }
  }

  checkIsLast(event: StepperSelectionEvent) {
    this.lastStepLabel = event.selectedStep.label;
    this.lastStep = event.selectedStep;
    this.isStepperLastIndex =
      event.selectedIndex === event.selectedStep._stepper._steps.length - 1;
  }

  changeStep(event: MatStep) {
    if (
      this.isStepperLastIndex &&
      event.stepControl.valid &&
      event.label === this.lastStepLabel
    ) {
      this.changeView('uploader');
    }
  }

  creatLastForm(): void {
    this.lastStepFormGroup = this.fb.group({
      stl: ['', false ? Validators.required : ''],
      photos: ['', false ? Validators.required : ''],
      additional: ['', false ? Validators.required : ''],
    });
  }

  dropped(file: File, type: Uploads) {
    this.lastStepFormGroup.get(type)?.setValue(file.name);
    this.uploadFiles[type] = file;
  }
}
