import { FormGroup } from '@angular/forms';

/**
	* This function to match between password and confirm password
	* @param {string} control1
	* @param {string} control2
	* @author Basel Elsawy
	* @copyright 3ddx
	* Created at     : 2021
	* Last modified  : 2021-10-28
*/

export function MustMatch(control1: string, control2: string) {
	return (formGroup: FormGroup): { [key: string]: boolean } | null => {
		const password = formGroup.controls[control1];
		const confirmPassword = formGroup.controls[control2];
		if (
			password.dirty &&
			confirmPassword.dirty &&
			password.value !== confirmPassword.value
		) {
			confirmPassword.setErrors({
				wrongPassword: true
			});
		}
    else {
      confirmPassword.setErrors(null);
    }
		return null;
	};
}
