import { Component, OnInit } from '@angular/core';
import {environment} from '../../../projects/connect/src/environments/environment';
import {HttpClient} from '@angular/common/http';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.css']
})
export class BannerComponent implements OnInit {
  bannerImage = environment.bannerImage;
  bannerURL = '';
  constructor(private http: HttpClient) { }

  ngOnInit(): void {
    this.http.get(environment.bannerNewUrl , {responseType: 'text'})
      .subscribe(
        data => {
          this.bannerURL = data.split('=')[1];
        },
        error => {
          console.error('[Error-Connect-v2]: Cannot read banner.properties will fallback to the assets version');
          this.http.get('./assets/banner.properties',  {responseType: 'text'}).subscribe(
            (data) => {
              this.bannerURL = data.split('=')[1];
            },
            error => {
              console.error('[Error-Connect-v2]: Cannot read banner.properties from assets too');
              this.bannerURL = './';
            }
            )
        }
        );
  }

}
