import { ControlContainer, FormControl, FormGroup } from '@angular/forms';
import { Component, OnInit, Input } from '@angular/core';
import { CStaticIds } from 'shared/shared-static-id/shared-static-id';
import { printerInfo } from 'shared/shared-static-data/printerBrandModels';

@Component({
  selector: 'app-custom-printer-form',
  templateUrl: './custom-printer-form.component.html',
  styleUrls: ['./custom-printer-form.component.css'],
})
export class CustomPrinterFormComponent implements OnInit {
    /*the width of the label by colms */
    @Input() labelWidth!:string;

    /*the width of the fieldboxes wrapper by colms */
    @Input() fieldBoxes!:string;
  // printer form group
  printerGroup: any;
  htmlID = CStaticIds;

  /** Printer models & brand */
  printerInformation = printerInfo;
  /** Printer brands */
  printerInfoKeys = Object.keys(this.printerInformation);
  /** Selected printer brand */
  selectedPrinterBrand: string = 'Stratasys';
  /** Printer models */
  printerModelArr = this.printerInformation['Stratasys'];

  constructor(public controlContainer: ControlContainer) {}

  ngOnInit(): void {
    // to make it custom form group
    this.printerGroup = this.controlContainer.control;

    this.createFormGroup('printerBrand', '');
    this.createFormGroup('printerModel', '');
    this.createFormGroup('printerResin', '');
    this.printerGroup.addValidators(this.validator);
  }

  /**
   *
   * @param controlName
   * @param value
   * @param validators
   */
  createFormGroup(controlName: string, value: any) {
    this.printerGroup.addControl(controlName, new FormControl(value));
  }

  /**
   * Updates the selected printer brand and retrieves the array of printer models for that brand.
   * @param event - The event object containing the value of the selected printer brand.
   * @returns void
   */
  onChangePrinterBrand(event: any): void {
    this.selectedPrinterBrand = event.value;
    // TypeScript may not know that `this.printerInformation` has a key for every possible value of `this.printerBrand`.
    // The following line of code might raise a TypeScript error. Ignore it using `@ts-expect-error`.
    this.printerModelArr = [
      // @ts-expect-error
      ...this.printerInformation[this.selectedPrinterBrand],
    ];
    // un select the model.
    this.printerGroup.controls['printerModel'].setValue('');
  }

  /**
   * custom validator to validate the printer brand and model
   * @param formGroup
   */
  validator(formGroup: FormGroup): { [key: string]: boolean } | null {
    const printerModelControl = formGroup.controls['printerModel'];
  if (!printerModelControl.value) {
      return {
        printerModelMandatory: true,
      };
    }
    return null;
  }
}
