import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FormBuilder, FormGroup} from '@angular/forms';
import {SubOrderType} from '../../shared-static-data/SubOrderType';
import {CStaticIds} from '../../shared-static-id/shared-static-id';
import {DateService} from '../../shared-services/date.service';

@Component({
  selector: 'app-advanced-search',
  templateUrl: './advanced-search.component.html',
  styleUrls: ['./advanced-search.component.css']
})
export class AdvancedSearchComponent implements OnInit {
  subOrderType = Object.values(SubOrderType);
  advancedSearchForm!: FormGroup;
  htmlID = CStaticIds;
  userType: any;
  today = new Date();
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param formBuilder for using FormBuilder
   * @param dateService for managing http methods
   * @param data for dialog data
   */
  constructor(
    public dialogRef: MatDialogRef<AdvancedSearchComponent>,
    private formBuilder: FormBuilder,
    private dateService: DateService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.userType = localStorage.getItem('type') ? localStorage.getItem('type') : '0';
    this.advancedSearchForm = this.formBuilder.group({
      patientName: [''],
      doctorName: [''],
      scanningCenter: [''],
      orderType: [''],
      dateStart: [''],
      dateEnd: ['']
    });
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * onSubmit set advanced search data
   * @return void
   */
  onSubmit(): void {
    this.advancedSearchForm.value.dateStart = this.dateService.dateToTimeStamp(this.advancedSearchForm.value.dateStart).toString();
    this.advancedSearchForm.value.dateEnd = this.dateService.dateToTimeStamp(this.advancedSearchForm.value.dateEnd).toString();

    this.dialogRef.close(this.advancedSearchForm.value);
  }
}
