import {Component, Inject, OnInit} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-feedback',
  templateUrl: './feedback.component.html',
  styleUrls: ['./feedback.component.css']
})
export class FeedbackComponent implements OnInit {
  feedbackForm!: FormGroup;
  isSubmitted = false;
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param formBuilder for using FormBuilder
   * @param data for dialog data
   */
  constructor(
    public formBuilder: FormBuilder,
    public dialogRef: MatDialogRef<FeedbackComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.feedbackForm = this.formBuilder.group({
      subject: ['', [Validators.required]],
      message: ['', [Validators.required]],
    });
  }
  /**
   * get form controls
   * @return void
   */
  get errorControl(): any {
    return this.feedbackForm.controls;
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * onSubmit check form validation
   * @return void
   */
  onSubmit(): void {
    this.isSubmitted = true;
    if (!this.feedbackForm.valid) {
      return;
    } else {
      this.isSubmitted = false;
      this.dialogRef.close(this.feedbackForm.value);
    }
  }
}
