import {Component, Inject, OnInit, OnDestroy} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {ActivatedRoute} from '@angular/router';
import {IPrintReceiptGet} from 'projects/connect/src/app/Models/Interfaces/Billing/PrintReceipt/PrintReceipt';
import {PrintReceiptService} from 'projects/connect/src/app/Services/BillingServices/PrintReceiptService/print.receipt.service';
import {Subscription} from 'rxjs/internal/Subscription';
import {ResponseCodeEnum} from 'shared/shared-enums/ResponseCodeEnum';
import {ServiceNameEnum} from 'shared/shared-enums/ServiceNameEnum';
import {handleOrdersBilling} from 'shared/shared-functions/HandleOrdersBilling';
import {Billing} from 'shared/shared-interfaces/billing';
import {CurrencyTypes} from './../../../shared-static-data/CurrencyTypes';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';
import {DateService} from '../../../shared-services/date.service';

/**
 * Copyright (c) 3ddx
 *
 * @summary Print Receipt Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-print-receipt',
  templateUrl: './print-receipt.component.html',
  styleUrls: ['./print-receipt.component.css']
})
export class PrintReceiptComponent implements OnInit, OnDestroy {
  logo: string = 'assets/Images/connectLogo.png';
  services: IPrintReceiptGet = {} as IPrintReceiptGet;
  subscription!: Subscription;
  billing: any;
  keyBilling!: number;
  currentDate: Date = new Date();
  htmlID = CStaticIds;
  constructor(
    public dialogRef: MatDialogRef<PrintReceiptComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private service: PrintReceiptService,
    public dateService: DateService
  ) {
  }

  ngOnInit(): void {
    this.getPrintReceipt();
  }

  getPrintReceipt(): void {
    this.services.ConditionsAnded = false;
    this.services.columns = [];
    this.services.orderId = this.data.orderId;
    this.services.subOrderEnumId = this.data.subOrderEnumId;
    this.services.serviceName = ServiceNameEnum.CONNECT_BILLING_SINGLE_SUBORDER;
    this.service.printReceiptOrder.services.push(this.services);
    this.subscription = this.service.getPrintReceiptService().subscribe(res => {
      if (res.responsCode === ResponseCodeEnum.OK) {
        this.keyBilling = +Object.keys(res.reponses[0].data[0]);
        this.billing = handleOrdersBilling([res.reponses[0].data[0][this.keyBilling]])[0];
        this.billing.name = localStorage.getItem('fName') + ' ' + localStorage.getItem('lName');
      }
    });
  }

  ngOnDestroy(): void {
    this.service.printReceiptOrder.services = [];
    this.services = {} as IPrintReceiptGet;
    this.subscription.unsubscribe();
  }
}
