import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  /**
   * The Requirements For The Construction Of The service
   * @param httpClient for performing http requests
   */
  constructor(public httpClient: HttpClient) {
  }


  /**
   * post method
   * @param APIPath
   * @param OBJ posted object
   * @return any
   */
  // tslint:disable-next-line:typedef
  post(APIPath: string, OBJ: any) {
    return this.httpClient.post<any>(`${environment.cpRootURL}${APIPath}`, OBJ, { withCredentials: true });
  }

  // tslint:disable-next-line:typedef
  postDownload(APIPath: string, OBJ: any) {
    return this.httpClient.post(`${environment.cpRootURL}${APIPath}`, OBJ, { observe: 'response', responseType: 'blob', withCredentials: true });
  }

}
