import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {TrackingNumberProviders} from '../../shared-static-data/TrackingNumberProviders';
import {CStaticIds} from '../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-track-shipped-order',
  templateUrl: './track-shipped-order.component.html',
  styleUrls: ['./track-shipped-order.component.css']
})
export class TrackShippedOrderComponent implements OnInit {
serviceProvider: any;
trackNumber: any;
htmlID = CStaticIds;
  constructor(  public dialogRef: MatDialogRef<TrackShippedOrderComponent>,
                @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit(): void {
    this.data.orderDetails.serviceProvider || this.data.orderDetails.serviceProvider === 0 ? this.serviceProvider = Object.values(TrackingNumberProviders).filter(c => c.fromBackEndValue === this.data.orderDetails.serviceProvider)[0].name : this.serviceProvider = 'N/A';
    this.data.orderDetails.trackNumber ? this.trackNumber = this.data.orderDetails.trackNumber : this.trackNumber = 'N/A';
  }

}
