import {Injectable} from '@angular/core';
import {HttpEvent, HttpHandler, HttpInterceptor, HttpRequest} from '@angular/common/http';
import {Observable} from 'rxjs';
import {IMain} from '../Models/Interfaces/Main/Main';
import {APIName} from '../../../../../shared/API/APIName';


@Injectable()
export class APIsInterceptor implements HttpInterceptor {
  constructor() {
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (!req.url.includes('.json') && !req.url.includes(APIName.uploadFile) && !req.url.includes(APIName.createNewPassword) && !req.url.includes('/assets/banner.properties') &&  !req.url.includes('localhost')) {
      const data = this.prepareAPIRequest(req.body);
      req = req.clone({body: data});
    }
    else if (req.url.includes(APIName.createNewPassword)){
      const newPassToken = localStorage.getItem('passChangeToken');
      if (newPassToken){
        req = req.clone({headers: req.headers.set('passchangetoken', newPassToken)});
      }
    }
    return next.handle(req);
  }

  prepareAPIRequest(body: any): IMain {
    return {
      internalUser: false,
      loginToken: localStorage.getItem('token') ?? '',
      requestType: body.requestType,
      userId: Number(localStorage.getItem('userCNId')) ? Number(localStorage.getItem('userCNId')) : -1,
      services: body.services
    };
  }
}


