import {
  Component,
  DoCheck,
  ElementRef,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  ViewChild,
  AfterViewChecked,
  ChangeDetectorRef,
} from '@angular/core';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ISharedDoctor } from 'shared/shared-interfaces/doctors';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { ActivatedRoute } from '@angular/router';
import { DisclaimerComponent } from 'shared/shared-components/shard-popup/disclaimer/disclaimer.component';
import { CStaticIds } from '../../../shared-static-id/shared-static-id';
import { AddDoctorComponent } from 'shared/shared-components/shard-popup/add-doctor/add-doctor.component';
import { environment } from '../../../../projects/connect/src/environments/environment';
import { IAddDoctor } from 'projects/connect/src/app/Models/Interfaces/AddDoctor/Adddoctor';
import { ServiceNameEnum } from 'shared/shared-enums/ServiceNameEnum';
import { addDoctorService } from 'shared/shared-services/doctor/add-doctor.service';
import { ERequestTypes } from 'projects/connect/src/app/Models/Enums/RequestTypes/RequestTypes';
import { ResponseCodeEnum } from 'shared/shared-enums/ResponseCodeEnum';
import { ToastrService } from 'shared/shared-services/toastr.service';
import { ThrowStmt } from '@angular/compiler';
import { MatStepper } from '@angular/material/stepper';
import { resetFakeAsyncZone } from '@angular/core/testing';

/**
 * Copyright (c) 3ddx
 *
 * @summary Add Order Form Component And Contain All Sub Order Components
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-11-28
 */

@Component({
  selector: 'app-add-order-form',
  templateUrl: './add-order-form.component.html',
  styleUrls: ['./add-order-form.component.css'],
})
export class AddOrderFormComponent implements OnInit, OnDestroy, DoCheck {
  @ViewChild('stepper') public stepper!: MatStepper;
  @ViewChild('btnTP') public btnTP: any;
  @ViewChild('btnSG') public btnSG: any;
  @ViewChild('fileInput') uploadFileSource!: ElementRef;
  @Output() ordersOBJ: EventEmitter<any> = new EventEmitter<any>();
  @Output() recalculateCost: EventEmitter<any> = new EventEmitter<any>();
  @Input() doctorsList!: ISharedDoctor[];
  @Input() buttonOrders: any;
  @Input() orderDetails: any;
  dialogRef!: MatDialogRef<AddDoctorComponent, any>;
  basicInfoFormGroup!: FormGroup;
  conversionForm!: FormGroup;
  treatmentPlanForm!: FormGroup;
  surgicalGuideForm!: FormGroup;
  restorationForm!: FormGroup;
  radiologyReportForm!: FormGroup;
  lastStepFormGroup!: FormGroup;
  doctorvalue: any;
  moreThanForm: boolean = true;
  maxillaAndMandible: boolean = true;
  checkTreatmentPlanAndSurgical: boolean = true;
  indexTypeOrder!: number;
  docId: any;
  restoration!: number;
  typeOrder: Array<string> = [];
  indexOfConversion!: number;
  indexOfTreatmentPlan!: number;
  indexOfSurgicalGuide!: number;
  srcResult!: File | null;
  orderFormRequest!: Object;
  indexOfFullGuideCase!: number;
  orderId!: number;
  validateTeeth!: boolean;
  typeUser!: number;
  modelScan: boolean = true;
  isAddAnotherOrder: boolean = false;
  htmlID = CStaticIds;
  submitTeeth: boolean = false;
  archOfInterest!: number;
  maxillaImplants!: number[];
  mandibleImplants!: number[];
  uploaded: boolean = false;
  Fileuploaded!: boolean;
  submitting = false;

  constructor(
    public fb: FormBuilder,
    public orderService: OrderSharedService,
    public dialog: MatDialog,
    public addDoctorService: addDoctorService,
    public route: ActivatedRoute,
    public toastService: ToastrService,
    public readonly changeDetectorRef: ChangeDetectorRef
  ) {}

  ngOnInit(): void {
    this.createBasicForm();
    this.creatLastForm();
    let type = localStorage.getItem('type') || '0';
    this.typeUser = +type;
    this.route.params.subscribe((res) => (this.orderId = res.orderId));
    if (
      this.orderId &&
      (this.orderDetails[0]?.fileNameConversion ||
        this.orderDetails[0]?.fileNameSurgicalGuide ||
        this.orderDetails[0]?.fileNameTreatmentPlan ||
        this.orderDetails[0]?.fileNameReport)
    ) {
      this.Fileuploaded = true;
      this.uploaded = true;
    }
    if (
      this.orderId &&
      this.orderDetails[0]?.surgicalGuideID &&
      !this.orderDetails[0].treatmentPlanID
    ) {
      this.orderService.buttons.treatmentPlan = true;
      this.createTreatmentPlanForm();
      this.checkExpressGuide();
    }
  }
  ngDoCheck(): void {
    this.chekFullGuided();
  }
  ngAfterViewChecked(): void {
    this.changeDetectorRef.detectChanges();
  }

  /**
   * This function check buttons and create sub order (treatmentPlan, surgicalGuide) form depend on guided full mouth radio button
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getGuidedFullMouth(rest: number) {
    this.restoration = rest;
    if (
      !this.orderService.buttons.treatmentPlan &&
      !this.btnTP._elementRef.nativeElement.disabled
    ) {
      this.orderService.buttons.treatmentPlan = true;
      this.createTreatmentPlanForm();
    }
    if (
      !this.orderService.buttons.surgicalGuide &&
      !this.btnSG._elementRef.nativeElement.disabled
    ) {
      this.orderService.buttons.surgicalGuide = true;
      this.createSurgicalGuide();
    }
  }

  /**
   * This function check buttons and create sub order form depend on select buttons
   * @param {string} name
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  checkButton(name: string): void {
    switch (name) {
      case 'fullGuideCase':
        this.orderService.buttons.fullGuideCase =
          !this.orderService.buttons.fullGuideCase;
        if (this.orderService.buttons.fullGuideCase) {
          this.orderService.buttons.conversion = true;
          this.orderService.buttons.treatmentPlan = true;
          this.orderService.buttons.surgicalGuide = true;
          this.moreThanForm = false;
        } else {
          this.orderService.buttons.conversion = false;
          this.orderService.buttons.treatmentPlan = false;
          this.orderService.buttons.surgicalGuide = false;
          this.moreThanForm = true;
        }
        if (
          this.orderService.buttons.treatmentPlan ||
          this.orderService.buttons.surgicalGuide
        ) {
          this.orderService.buttons.teethChart = true;
        }
        this.createConversionForm();
        this.createTreatmentPlanForm();
        this.createSurgicalGuide();
        this.checkOrders(!this.orderService.buttons.conversion, 'conversion');
        this.checkOrders(
          !this.orderService.buttons.treatmentPlan,
          'treatmentPlan'
        );
        this.checkOrders(
          !this.orderService.buttons.surgicalGuide,
          'surgicalGuide'
        );
        break;
      case 'conversion':
        this.orderService.buttons.conversion =
          !this.orderService.buttons.conversion;
        this.orderService.buttons.fullGuideCase = false;
        this.checkOrders(!this.orderService.buttons.conversion, name);
        this.createConversionForm();
        this.checkExpressGuide();
        this.orderService.buttons.conversion
          ? (this.moreThanForm = false)
          : (this.moreThanForm = true);
        break;
      case 'treatmentPlan':
        this.orderService.buttons.treatmentPlan =
          !this.orderService.buttons.treatmentPlan;
        this.orderService.buttons.fullGuideCase = false;
        if (this.orderService.buttons.treatmentPlan) {
          this.orderService.buttons.teethChart = true;
        } else if (
          this.orderService.buttons.restorations &&
          !this.orderService.buttons.treatmentPlan
        ) {
          this.restorationForm?.controls.restType.patchValue(1);
        } else if (
          !this.orderService.buttons.treatmentPlan &&
          !this.orderService.buttons.surgicalGuide &&
          !this.orderService.buttons.restorations
        ) {
          this.orderService.buttons.teethChart = false;
        }
        this.checkOrders(!this.orderService.buttons.treatmentPlan, name);
        this.createTreatmentPlanForm();
        this.checkExpressGuide();
        break;
      case 'surgicalGuide':
        this.orderService.buttons.surgicalGuide =
          !this.orderService.buttons.surgicalGuide;
        this.orderService.buttons.fullGuideCase = false;
        if (this.orderService.buttons.surgicalGuide) {
          this.orderService.buttons.teethChart = true;
          this.modelScan = false;
          this.orderService.surgicalChecked =
            !this.orderService.surgicalChecked;
          if (!this.buttonOrders.treatmentPlan) {
            this.orderService.buttons.treatmentPlan = true;
          }
        } else if (
          this.orderService.buttons.restorations &&
          !this.orderService.buttons.surgicalGuide
        ) {
          this.restorationForm?.controls.restType.patchValue(1);
        } else if (
          !this.orderService.buttons.treatmentPlan &&
          !this.orderService.buttons.surgicalGuide &&
          !this.orderService.buttons.restorations
        ) {
          this.orderService.buttons.teethChart = false;
        }
        if (!this.orderService.buttons.surgicalGuide) {
          this.modelScan = true;
        }
        this.checkOrders(!this.orderService.buttons.surgicalGuide, name);
        this.createSurgicalGuide();
        this.createTreatmentPlanForm();
        this.checkExpressGuide();
        break;
      case 'restorations':
        this.orderService.buttons.restorations =
          !this.orderService.buttons.restorations;
        if (this.orderService.buttons.restorations) {
          this.orderService.buttons.teethChart = true;
          this.maxillaAndMandible = false;
        } else if (
          !this.orderService.buttons.treatmentPlan &&
          !this.orderService.buttons.surgicalGuide &&
          !this.orderService.buttons.restorations
        ) {
          this.orderService.buttons.teethChart = false;
          this.maxillaAndMandible = true;
        }
        this.createRestoration();
        break;
      case 'radiologyReport':
        this.orderService.buttons.radiologyReport =
          !this.orderService.buttons.radiologyReport;
        this.createRadiologyReport();
        break;
      case 'otherServices':
        this.orderService.buttons.otherServices =
          !this.orderService.buttons.otherServices;
        break;
      default:
    }
    if (
      this.orderService.buttons.surgicalGuide &&
      this.orderService.buttons.treatmentPlan
    ) {
      this.treatmentPlanForm.controls.answerSG.setValue(2);
    } else if (this.orderService.buttons.treatmentPlan) {
      this.treatmentPlanForm.controls.answerSG.setValue(1);
    }
  }

  chekFullGuided() {
    if (
      this.orderService.buttons.conversion &&
      this.orderService.buttons.treatmentPlan &&
      this.orderService.buttons.surgicalGuide
    ) {
      this.orderService.buttons.fullGuideCase = true;
    }
  }

  /**
   * This function it contains basic info form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createBasicForm(): void {
    this.basicInfoFormGroup = this.fb.group({
      doctor: '',
      billing: 0,
      gender: 2,
      patientName: ['', Validators.required],
    });
  }

  /**
   * This function it contains conversion form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createConversionForm(): void {
    this.conversionForm = this.fb.group({
      software: [1, Validators.required],
      format: [1, Validators.required],
      maxilla: [20, Validators.required],
      mandible: [20, Validators.required],
      typeMaxilla: [1, Validators.required],
      typeMandible: [1, Validators.required],
      portableVersion: [1, Validators.required],
      modelScan: [0, Validators.required],
      separateDoubleArchOrder: false,
      generalComment: '',
    });
  }

  /**
   * This function it contains treatment plan form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createTreatmentPlanForm(): void {
    this.treatmentPlanForm = this.fb.group({
      isReviewTP: false,
      software: [3, Validators.required],
      portableVersion: [1, Validators.required],
      implantType: '',
      prosthesisType: '',
      surgicalPlan: '',
      answerSG: [1, Validators.required],
      agree: [false, Validators.required],
      generalComment: '',
    });
    this.checkAnswerSG();

    if (this.buttonOrders.conversion) {
      this.treatmentPlanForm.controls.software.patchValue(
        this.orderDetails[0].softwareEnumId
      );
    }
  }

  checkAnswerSG() {
    this.buttonOrders.surgicalGuide
      ? this.treatmentPlanForm?.controls.answerSG.setValue(2)
      : this.treatmentPlanForm?.controls.answerSG.setValue(1);
    this.treatmentPlanForm?.controls.answerSG.valueChanges.subscribe((res) => {
      if (res === 2) {
        this.orderService.buttons.surgicalGuide = true;
        this.createSurgicalGuide();
      } else {
        this.orderService.buttons.surgicalGuide = false;
        this.surgicalGuideForm?.reset();
        if (
          this.orderService.buttons.restorations &&
          this.restorationForm.controls.restType.value === 2
        ) {
          this.restorationForm.controls.restType.patchValue(1);
        }
      }
    });
  }

  checkOwnCopyTP(): any {
    let ownType = this.treatmentPlanForm?.controls.isReviewTP.value;
    this.treatmentPlanForm?.controls.isReviewTP.valueChanges.subscribe(
      (res) => {
        if (res) {
          this.stepper.selectedIndex = 2;
          ownType = true;
        } else {
          ownType = false;
        }
      }
    );
    return ownType;
  }

  /**
   * This function it contains surgical guide form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createSurgicalGuide(): void {
    this.surgicalGuideForm = this.fb.group({
      dds: [false, Validators.required],
      services: [ this.restorationForm?.controls?.restType.value != 2 ? 1 : 3],
      software: [3, Validators.required],
      rush: [1, Validators.required],
      guided: [1, Validators.required],
      intraoralScan: [1, Validators.required],
      typeofGuide: [1, Validators.required],
      extracomponents: this.fb.array([]),
      guidedSurgeryKitType: 0,
      // SG Extra Components
      otherDesc: '',
      scheduledSurgeryData: '',
      scheduledSurgeryTime: '',
      generalComment: '',
    });
    if (this.buttonOrders.treatmentPlan || this.buttonOrders.conversion) {
      this.surgicalGuideForm.controls.software.patchValue(
        this.orderDetails[0].softwareEnumId
      );
    }
  }
  /**
   * on select if the surgical guide will be a digital design order or manufacture order
   * @param event
   */
  onDigitalSurgicalGuide(event: any) {
    this.surgicalGuideForm.patchValue({ dds: event.value });
  }
  /**
   * This function it contains restorations form
   * run to open the digital services tabs
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createRestoration(): void {
    this.restorationForm = this.fb.group({
      restType: [0, Validators.required],
      isRush: [true, Validators.required],
    });
  }

  createGFMRForm() {
    // Keep the old value of restType
    const restTypeValue =
      this.restorationForm &&
      this.restorationForm.value &&
      this.restorationForm.value.restType
        ? this.restorationForm.value.restType
        : 0;

    this.restorationForm = this.fb.group({
      dds: [false, Validators.required],
      restType: [restTypeValue, Validators.required],
      finalRestoration: [0, Validators.required],
      // deliverable form group
      deliverablesgroup: this.fb.group({}),
      // 3D printer form group
      printerGroup: this.fb.group({}),
      archOfInterest: [0, Validators.required],
      chairSideAssistance: [0, Validators.required],
      pouring: [0, Validators.required],
      retentionType: [0, Validators.required],
      retentionTypeSpecify: [
        { value: '', disabled: true },
        Validators.required,
      ],
      lowerFile: [null ,Validators.required],
      upperFile: [null ,Validators.required],
      biteFile: [null ,Validators.required],
      FacialScan: [null],
      haveModelScans: [1],
      haveDSDPhotos: [0],
      DSDphotosFiles: [null],
      crown: [1, Validators.required],
      crownSpecify: [{ value: '', disabled: true }],
      abutment: [0, Validators.required],
      abutmentSpecify: [{ value: '', disabled: true }, Validators.required],
      shade: [null, Validators.required],
      shadeOther: ['', Validators.required],
      teeth: ['', Validators.required],
      oral: [1, Validators.required],
      isRush: [0, Validators.required],
      generalComment: '',
    });
  }

  /**
   * when choose final or temp restoration
   * the GFMR form controls will remove
   */
  removeGFMRControls() {
    if (this.restorationForm.controls['deliverablesgroup']) {
      this.restorationForm = this.fb.group({
        // Keep the old value of restType
        restType: [this.restorationForm.value.restType, Validators.required],
        isRush: [true, Validators.required],
      });
    }
  }

  /**
   * This function it contains radiology report form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  createRadiologyReport(): void {
    this.radiologyReportForm = this.fb.group({
      birthdate: ['', Validators.required],
      relevant: ['', Validators.required],
      implantPlanned: [1, Validators.required],
      evaluateExistingImplant: [1, Validators.required],
      sinusEvaluation: [1, Validators.required],
      tmjevaluation: [1, Validators.required],
      ruleOutPathology: [2, Validators.required],
      airWayEvaluation: [1, Validators.required],
      rushCase: [1, Validators.required],
      imagedate: ['', Validators.required],
      BasicReport: [1, Validators.required],
      generalComment: '',
    });
  }

  /**
   * This function it contains last form form
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  creatLastForm(): void {
    this.lastStepFormGroup = this.fb.group({
      uploadFile: '',
    });
  }

  /**
   * This function check common inputs
   * @param {boolean} checkTypeOrder  
   * @param {string} name
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  checkOrders(checkTypeOrder: boolean, name: string) {
    this.indexOfConversion = this.typeOrder.indexOf('conversion');
    this.indexOfTreatmentPlan = this.typeOrder.indexOf('treatmentPlan');
    this.indexOfSurgicalGuide = this.typeOrder.indexOf('surgicalGuide');
    this.indexOfFullGuideCase = this.typeOrder.indexOf('fullGuideCase');
    this.checkDataInArray(checkTypeOrder, name);
    this.checkConditionSoftwareVal();
    this.typeOrder = checkTypeOrder
      ? this.typeOrder.filter((order: any) => order !== name)
      : this.typeOrder;
  }

  checkArchOfInterestValue(): void {
    let stepperIndex;
    this.orderId ? (stepperIndex = 1) : (stepperIndex = 2);
    if (
      this.restorationForm.controls.restType.value === 2 &&
      this.orderService.buttons.teethChart
    ) {
      this.maxillaImplants = this.orderService.teethChart.implantSites.filter(
        (implant) => implant <= 16
      );
      this.mandibleImplants = this.orderService.teethChart.implantSites.filter(
        (implant) => implant > 16
      );
      if (
        (this.archOfInterest === 0 &&
          !this.orderId &&
          this.maxillaImplants.length < 4) ||
        (this.archOfInterest === 1 &&
          !this.orderId &&
          this.mandibleImplants.length < 4) ||
        (this.archOfInterest === 2 &&
          !this.orderId &&
          (this.maxillaImplants.length < 4 || this.mandibleImplants.length < 4))
      ) {
        this.stepper.selectedIndex = stepperIndex;
        this.submitTeeth = true;
      }
    }
  }

  resetForm(): void {
    if (this.orderService.buttons.conversion) {
      this.conversionForm.reset({
        software: 1,
        format: 1,
        maxilla: 20,
        mandible: 20,
        typeMaxilla: 1,
        typeMandible: 1,
        portableVersion: 1,
        modelScan: 0,
        separateDoubleArchOrder: false,
      });
    }
    if (this.orderService.buttons.radiologyReport) {
      this.radiologyReportForm.reset({
        birthdate: '',
        relevant: '',
        implantPlanned: 1,
        evaluateExistingImplant: 1,
        sinusEvaluation: 1,
        tmjevaluation: 1,
        ruleOutPathology: 2,
        airWayEvaluation: 1,
        rushCase: 1,
        imagedate: '',
        BasicReport: 1,
      });
    }
    if (this.orderService.buttons.surgicalGuide) {
      this.surgicalGuideForm.reset({
        software: 3,
        dds: this.surgicalGuideForm.get('dds')?.value,
        services: this.fb.array([
          'Surgical Guide Design',
          'Surgical Guide and Bone Reduction Guide Design-Magnetix',
          'Surgical Guide , Bone Reduction and PMMA Design',
        ]),
        rush: 1,
        guided: 1,
        intraoralScan: 1,
        typeofGuide: 1,
        extracomponents: this.fb.array([]),
        guidedSurgeryKitType: 0,
        otherDesc: '',
        scheduledSurgeryData: '',
        scheduledSurgeryTime: '',
      });
    }
    if (this.orderService.buttons.treatmentPlan) {
      this.treatmentPlanForm.reset({
        isReviewTP: false,
        software: 3,
        portableVersion: 1,
        implantType: '',
        prosthesisType: '',
        surgicalPlan: '',
        answerSG: this.treatmentPlanForm.controls.answerSG.value,
        agree: false,
      });
    }
    if (this.orderService.buttons.restorations) {
      this.restorationForm.reset({
        restType: this.restorationForm.value.restType,
        pouring: 0,
        retentionType: 0,
        retentionTypeSpecify: { value: '', disabled: true },
        crown: 1,
        crownSpecify: { value: '', disabled: true },
        abutment: 0,
        abutmentSpecify: { value: '', disabled: true },
        shade: null,
        teeth: '',
        oral: 1,
        isRush: 0,
        archOfInterest: 0,
        chairSideAssistance: 0,
        lowerFile: null,
        upperFile: null,
        biteFile: null,
        DSDphotosFiles: null,
        Abutment: null,
        Cement: null,
        SCrew: null,
        SelsctedCrown: null,
        Collar_Buccal: null,
        CollarLingual: null,
        Scanbody: '',
        ImplantKind: '',
        EmergenceProfile: null,
        TissuePressure: null,
        IsNest: null,
        BuccalMeasurement: null,
        LingualMeasurement: null,
        TissueMeasurement: null,
        RXNote: '',
        generalComment: '',
      });
    }
  }

  checkDataInArray(checkTypeOrder: boolean, name: string): void {
    if (!checkTypeOrder) {
      this.typeOrder.push(name);
    } else {
      if (name === 'conversion') {
        this.typeOrder.splice(this.indexOfConversion, 1);
      } else if (name === 'treatmentPlan') {
        this.typeOrder.splice(this.indexOfTreatmentPlan, 1);
      } else if (name === 'surgicalGuide') {
        this.typeOrder.splice(this.indexOfSurgicalGuide, 1);
      }
    }
  }

  checkConditionSoftwareVal(): void {
    if (this.typeOrder.includes('conversion') || this.typeOrder.length === 3) {
      this.moreThanForm = false;
      this.checkTreatmentPlanAndSurgical = false;
    } else {
      if (this.typeOrder.includes('treatmentPlan')) {
        this.checkTreatmentPlanAndSurgical = false;
        this.moreThanForm = true;
      } else if (
        !this.typeOrder.includes('treatmentPlan') &&
        this.typeOrder.includes('surgicalGuide')
      ) {
        this.checkTreatmentPlanAndSurgical = true;
        this.moreThanForm = false;
      }
    }
    this.typeOrder.length > 1
      ? (this.maxillaAndMandible = false)
      : (this.maxillaAndMandible = true);
  }

  validationTeeth(): void {
    this.validateTeeth =
      this.orderService.teethChart.implantSites.length === 0 ||
      (this.orderService.buttons.restorations &&
        (this.orderService.teethChart.abutmentSites.length === 0 ||
          this.orderService.teethChart.crownSites.length === 0));
  }

  /**
   * This function does upload files
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  onFileSelected() {
    const inputNode: any = document.querySelector(
      '#' + this.htmlID.Input + 'result-file'
    );
    this.srcResult = inputNode.files[0];
    if (this.srcResult) this.uploaded = true;
    else {
      this.uploaded = false;
    }
  }

  getGFMRArchOfInterst(): any {
    this.orderService.getImplantsArches();
    if (
      this.orderService.buttons.restorations &&
      this.restorationForm.controls.restType.value === 2
    ) {
      if (
        this.orderService.teethImplantSites.maxilla.length >= 4 &&
        this.orderService.teethImplantSites.mandible.length >= 4
      ) {
        this.restorationForm.controls.archOfInterest.setValue(2);
      } else if (this.orderService.teethImplantSites.maxilla.length >= 4) {
        this.restorationForm.controls.archOfInterest.setValue(0);
      } else if (this.orderService.teethImplantSites.mandible.length >= 4) {
        this.restorationForm.controls.archOfInterest.setValue(1);
      }
    }
  }
  /**
   * This function does output data order form from AddOrderFormComponent to AddOrderComponent
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  submit(): void | boolean {
    this.submitting = true;
    this.getGFMRArchOfInterst();
    this.orderFormRequest = {
      basicInfoFormGroup: this.basicInfoFormGroup
        ? this.basicInfoFormGroup.value
        : null,
      conversionForm: this.orderService.buttons.conversion
        ? this.conversionForm.value
        : null,
      treatmentPlanForm: this.orderService.buttons.treatmentPlan
        ? this.treatmentPlanForm.value
        : null,
      surgicalGuideForm: this.orderService.buttons.surgicalGuide
        ? this.surgicalGuideForm.value
        : null,
      restorationForm: this.orderService.buttons.restorations
        ? this.restorationForm.value
        : null,
      radiologyReportForm: this.orderService.buttons.radiologyReport
        ? this.radiologyReportForm.value
        : null,
      lastStepFormGroup: this.lastStepFormGroup
        ? this.lastStepFormGroup.value
        : null,
      srcResult: this.srcResult ? this.srcResult : null,
      isAddAnotherOrder: this.isAddAnotherOrder,
    };
    this.ordersOBJ.emit(this.orderFormRequest);
    if (this.isAddAnotherOrder) {
      this.srcResult = null;
      this.uploaded = false;
      this.stepper.selectedIndex = 0;
      this.createBasicForm();
      this.creatLastForm();
      this.resetForm();
      this.isAddAnotherOrder = false;
      this.orderService.teethChart = {
        abutmentSites: [],
        crownSites: [],
        implantSites: [],
        missingTeeth: [],
        teethToBeExtracted: [],
      };
      this.orderService.teethImplantSites = {
        mandible: [],
        maxilla: [],
      };
    }

    setTimeout(() => {this.submitting = false}, 20000);
  }

  disclaimer() {
    this.dialog.open(DisclaimerComponent);
  }

  calculateCost() {
    this.getGFMRArchOfInterst();
    this.orderFormRequest = {
      basicInfoFormGroup: this.basicInfoFormGroup
        ? this.basicInfoFormGroup.value
        : null,
      conversionForm: this.orderService.buttons.conversion
        ? this.conversionForm.value
        : null,
      treatmentPlanForm: this.orderService.buttons.treatmentPlan
        ? this.treatmentPlanForm.value
        : null,
      surgicalGuideForm: this.orderService.buttons.surgicalGuide
        ? this.surgicalGuideForm.value
        : null,
      restorationForm: this.orderService.buttons.restorations
        ? this.restorationForm.value
        : null,
      radiologyReportForm: this.orderService.buttons.radiologyReport
        ? this.radiologyReportForm.value
        : null,
      lastStepFormGroup: this.lastStepFormGroup
        ? this.lastStepFormGroup.value
        : null,
      srcResult: this.srcResult ? this.srcResult : null,
    };
    this.recalculateCost.emit(this.orderFormRequest);
  }

  ngOnDestroy() {
    this.orderService.buttons = {
      fullGuideCase: false,
      conversion: false,
      treatmentPlan: false,
      surgicalGuide: false,
      restorations: false,
      radiologyReport: false,
      teethChart: false,
      otherServices: false,
    };
  }

  addAnotherOrder() {
    this.isAddAnotherOrder = true;
    if (this.uploadFileSource) {
      this.uploadFileSource.nativeElement.value = '';
    }
  }

  restTypeValue(event: any): void {
    this.restorationForm.controls.restType.setValue(event.value);
    // In case of GFMR
    if (event.value === 2) {
      this.orderService.buttons.treatmentPlan = true;
      this.orderService.buttons.surgicalGuide = true;
      this.createGFMRForm();
      // Do You Plan To Order a Surgical Guide? => yes
      this.treatmentPlanForm?.controls.answerSG.setValue(2);
      this.createSurgicalGuide();
      this.createTreatmentPlanForm();

      // Choose treatmentPlan and surgicalGuide orders
      this.checkOrders(false, 'treatmentPlan');
      this.checkOrders(false, 'surgicalGuide');
    }
    else if(event.value==0 ||event.value==1 ||event.value==3 ){
      if (
        this.orderService.buttons.surgicalGuide &&
        this.orderService.buttons.treatmentPlan    )
       {
        this.orderService.buttons.treatmentPlan = true;
        this.orderService.buttons.surgicalGuide = true;
        // Do You Plan To Order a Surgical Guide? => yes
        this.treatmentPlanForm?.controls.answerSG.setValue(2);
        this.createSurgicalGuide();
        this.createTreatmentPlanForm();

      // Choose treatmentPlan and surgicalGuide orders
      this.checkOrders(false, 'treatmentPlan');
      this.checkOrders(false, 'surgicalGuide');
      }
    }
     else {
      this.removeGFMRControls();
      if (
        this.orderService.buttons.surgicalGuide &&
        this.orderService.buttons.treatmentPlan &&
        !this.orderService.buttons.fullGuideCase
      ) {
        this.orderService.buttons.surgicalGuide = false;
        this.orderService.buttons.treatmentPlan = false;
        this.checkOrders(true, 'treatmentPlan');
        this.checkOrders(true, 'surgicalGuide');
      }

      // Do You Plan To Order a Surgical Guide? => no
      this.treatmentPlanForm?.controls.answerSG.setValue(1);
    }
    this.checkConditionSoftwareVal();
  }

  navigateToRadiology(e: any) {
    if (e.checked) {
      this.orderService.buttons.radiologyReport = true;
      this.createRadiologyReport();
      this.stepper.selectedIndex = this.stepper.selectedIndex;
    }
  }

  openAddDoctor() {
    this.dialogRef = this.dialog.open(AddDoctorComponent, {
      disableClose: true,
      data: {
        logo: environment.connectLogo,
      },
    });

    this.dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        let doctorService: IAddDoctor = {
          ConditionsAnded: false,
          columns: ['userCNId'],
          serviceName: ServiceNameEnum.ADD_Doctor,
          fName: result.firstName,
          lName: result.lastName,
          isPortableVersion: false,
          countryEnumId: 0,
          userCNAdresses: null,
          userCNCreditCards: null,
          userCNEnumId: Number(localStorage.getItem('type')),
          userCNEmails: [{ email: result.email, emailEnumId: 10001 }],
          userCNTelecoms: [{ telecom: result.phone, telecomEnumId: 11000 }],
        };

        this.addDoctorService
          .sendaddDoctor({
            requestType: ERequestTypes.ADD,
            services: [doctorService],
          })
          .subscribe((res) => {
            if (res.responsCode === ResponseCodeEnum.OK) {
              this.toastService.addAlert(
                'Doctor added successfully',
                'Add Doctor',
                'start'
              );
              this.docId = res.reponses[0].data[0].userCNId;
              this.doctorvalue = {
                docID: this.docId,
                doctorFirstName: result.firstName,
                doctorLastName: result.lastName,
              };

              this.doctorsList.push(this.doctorvalue);
              this.basicInfoFormGroup.patchValue({ doctor: this.docId });
            } else if (
              res.responsCode ===
              ResponseCodeEnum.Registeration_Failed_Email_Exist
            ) {
              this.toastService.errorAlert(
                'This Email Already Exist',
                'Add Doctor'
              );
            }
          });
      }
    });
  }

  checkExpressGuide(): void {
    if (!this.buttonOrders.conversion) {
      if (
        this.orderService.buttons.surgicalGuide ||
        this.orderService.buttons.treatmentPlan
      ) {
        this.conversionForm?.controls?.software.patchValue(3);
        this.conversionForm?.controls?.format.patchValue(8);
        this.surgicalGuideForm?.controls?.software.patchValue(3);
        this.treatmentPlanForm?.controls?.software.patchValue(3);
      } else if (this.orderService.buttons.conversion) {
        this.conversionForm.controls.software.patchValue(1);
        this.conversionForm?.controls?.format.patchValue(1);
      }
    }
  }
}
