import { Component, Input, OnInit } from '@angular/core';
import { CStaticIds } from 'shared/shared-static-id/shared-static-id';
import { ServicesTeethChartComponent } from '../services-teeth-chart/services-teeth-chart.component';
import { MatDialog } from '@angular/material/dialog';
import { DigitalServiceSiteTypes } from 'shared/shared-enums/digitalServiceSiteTypes';
import { ControlContainer, FormControl, FormGroup } from '@angular/forms';

@Component({
  selector: 'app-custom-tooth-chart',
  templateUrl: './custom-tooth-chart.component.html',
  styleUrls: ['./custom-tooth-chart.component.css']
})
export class CustomToothChartComponent implements OnInit {

  toothChartGroup:any;
  @Input() toothType!:string;
  
  htmlID = CStaticIds;
  constructor(public dialog: MatDialog,public controlContainer: ControlContainer) { }

  ngOnInit(): void {
    // to make it custom form group
    this.toothChartGroup = this.controlContainer.control;

    this.createFormGroup('teeth', '');
    this.createFormGroup('note', '');
  }

  createFormGroup(controlName: string, value: any) {
    this.toothChartGroup.addControl(controlName, new FormControl(value));
  }
  /**
   * Opens a dialog box showing a teeth chart for selecting specific teeth and associated services.
   *
   * @param serviceType - The type of dental service that is being performed.
   * @returns void
   */
  toothChartClick() {
    const toothdialogRef = this.dialog.open(ServicesTeethChartComponent, {
      width: '90vw',
      data: {
        serviceName: this.toothType,
        serviceSite: DigitalServiceSiteTypes.perTooth,
        specifiedTeeth:
          this.toothChartGroup.get('teeth')?.value || [],
      },
    });
    toothdialogRef.afterClosed().subscribe((result: any) => {
      this.toothChartGroup.get('teeth')?.setValue(result);
    });
  };
}
