import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {SubOrderType} from '../../shared-static-data/SubOrderType';
import {CStaticIds} from '../../shared-static-id/shared-static-id';

@Component({
    selector: 'app-prepare-package',
    templateUrl: './prepare-package.component.html',
    styleUrls: ['./prepare-package.component.css']
})
export class PreparePackageComponent implements OnInit {
    isModelScan: boolean = false;
    isRestModelScan: boolean = false;
    htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialogRef for dialog reference
   * @param data for dialog data
   */
    constructor(
        public dialogRef: MatDialogRef<PreparePackageComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any,
    ) {
    }

    ngOnInit(): void {
    }
  /**
   * close dialog
   * @return void
   */
    cancel(): void {
        this.dialogRef.close();
    }
}
