import {EOrderBackgroundColorStatus} from '../shared-enums/order-background-color-status';
import {EOrderTextColorStatus} from '../shared-enums/order-text-color-status';
import {OrderStatusAction} from '../shared-enums/order-status-action';

export class CaseStatus {
  status: string;
  subOrderName: string;
  uploadType?: string;
  subOrderId: number;
  bgColor: EOrderBackgroundColorStatus;
  textColor: EOrderTextColorStatus;
  action?: OrderStatusAction;
  hasAction: boolean;

  constructor(
    status: string,
    subOrderName: string,
    uploadType: string,
    subOrderId: number,
    bgColor: EOrderBackgroundColorStatus,
    textColor: EOrderTextColorStatus,
    action: OrderStatusAction,
    hasAction: boolean) {
    this.status = status;
    this.subOrderName = subOrderName;

    this.subOrderId = subOrderId;
    this.bgColor = bgColor;
    this.textColor = textColor;
    if (uploadType) {
      this.uploadType = uploadType;
    }
    if (action) {
      this.action = action;
    }
    this.hasAction = hasAction;
  }
}
