import { HttpClient, HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { SharedComponentsModule } from 'shared/shared-components/shared-components.module';
import { AngularMaterialModule } from 'shared/shared-modules/angular-material.module';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { DatePipe } from '@angular/common';
import { AddOrderPageComponent } from './add-order-page/add-order-page.component';
import { AddOrderComponent } from 'projects/connect/src/app/Pages/ConnectModules/connect-pages/add-order-pages/add-order/add-order.component';
import { APIsInterceptor } from 'projects/connect/src/app/Interceptors/APis.interceptor';
import { ProgressBarComponent } from './progress-bar/progress-bar.component';


export function createTranslateLoader(http: HttpClient): TranslateHttpLoader {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    AddOrderPageComponent,
    AddOrderComponent,
    ProgressBarComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    SharedComponentsModule,
    AppRoutingModule,
    HttpClientModule,
    AngularMaterialModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: (createTranslateLoader),
        deps: [HttpClient]
      }
    }),
  ],
  providers: [DatePipe,
    {
      provide: HTTP_INTERCEPTORS, useClass: APIsInterceptor, multi: true
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
