import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {OrderStatusAction} from '../../../../shared-enums/order-status-action';
import {MatDialog, MatDialogRef} from '@angular/material/dialog';
import {PreparePackageComponent} from '../../../prepare-package/prepare-package.component';
import {PrepareTrackingNumberComponent} from '../../../prepare-tracking-number/prepare-tracking-number.component';
import {UploadFileComponent} from '../../../upload-file/upload-file.component';
import {IGetSurgicalGuide} from '../../../../shared-interfaces/get-surgical-guide';
import {UploadGFMRFilesComponent} from '../../../upload-gfmrfiles/upload-gfmrfiles.component';
import {ViewSubOrderNotesComponent} from '../../../view-sub-order-notes/view-sub-order-notes.component';
import {AcceptRejectRestorationGfmrComponent} from '../../../accept-reject-restoration-gfmr/accept-reject-restoration-gfmr.component';
import {CStaticIds} from '../../../../shared-static-id/shared-static-id';
import {TrackShippedOrderComponent} from '../../../track-shipped-order/track-shipped-order.component';
import {SubOrderType} from '../../../../shared-static-data/SubOrderType';
import {OrderListService} from '../../../../../projects/connect/src/app/Services/OrderServices/OrderListService/order-list.service';

@Component({
  selector: 'app-order-details-card',
  templateUrl: './order-details-card.component.html',
  styleUrls: ['./order-details-card.component.css']
})
export class OrderDetailsCardComponent implements OnInit{
  @Input() orderDetails: any;
  @Input() orderId!: number;
  @Input() logo!: string;
  @Input() patientName!: string;
  @Input() scanningCenter!: string;
  @Input() surgicalGuide = {} as IGetSurgicalGuide;
  @Output() onConfirmingSurgicalGuide: EventEmitter<any> = new EventEmitter();
  @Output() onSendingStoneModel: EventEmitter<any> = new EventEmitter();
  @Output() onUploadingFiles: EventEmitter<any> = new EventEmitter();
  @Output() onPrepareTrackingNumberEmmit: EventEmitter<any> = new EventEmitter();
  @Output() onDownloadFile: EventEmitter<any> = new EventEmitter();
  @Output() onUploadFileEmmit: EventEmitter<any> = new EventEmitter();
  @Output() onUploadGRMRFilesEmmit: EventEmitter<any> = new EventEmitter();
  @Output() onAcceptRejectRestorationGFMREmmit: EventEmitter<any> = new EventEmitter();
  action = OrderStatusAction;
  dialogRef!: MatDialogRef<PreparePackageComponent, any>;
  dialogPrepareTrackingRef!: MatDialogRef<PrepareTrackingNumberComponent, any>;
  dialogUploadFileRef!: MatDialogRef<UploadFileComponent, any>;
  dialogUploadGFMRFileRef!: MatDialogRef<UploadGFMRFilesComponent, any>;
  dialogSubOrderNotesRef!: MatDialogRef<ViewSubOrderNotesComponent, any>;
  dialogAcceptRejectRestorationGFMRRef!: MatDialogRef<AcceptRejectRestorationGfmrComponent, any>;
  dialogTrackShippedOrder!: MatDialogRef<TrackShippedOrderComponent, any>;
  htmlID = CStaticIds;
  subOrderStatus!: string;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialog for using angular material dialog
   * @param orderListService
   */
  constructor(public dialog: MatDialog, public orderListService: OrderListService) {
  }

  ngOnInit(): void {
  }
  /**
   * emit confirming surgical guide
   * @return void
   */
  confirmSurgicalGuide(): void {
    this.onConfirmingSurgicalGuide.emit(null);
  }
  /**
   * emit sending stone model
   * @return void
   */
  sendStoneModel(): void {
    this.onSendingStoneModel.emit(null);
  }

  /**
   * emit uploading missing files
   * @return void
   */
  uploadMissingFiles(): void {
    this.onUploadingFiles.emit(null);
  }
  /**
   * prepare package pop-up
   * @return void
   */
  onPreparePackage(): void {
    this.dialogRef = this.dialog.open(PreparePackageComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
        orderId: this.orderId,
        patientName: this.patientName,
        scanningCenter: this.scanningCenter,
        subOrderName: this.orderDetails.subOrderStatus.subOrderName
      }
    });
  }
  /**
   * View SubOrder Notes pop-up
   * @return void
   */
  onViewNotes(): void {
    this.dialogSubOrderNotesRef = this.dialog.open(ViewSubOrderNotesComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
        orderDetail: this.orderDetails
      }
    });
  }
  /**
   * Prepare Tracking Number Notes pop-up
   * @return void
   */
  onPrepareTrackingNumber(): void {
    this.dialogPrepareTrackingRef = this.dialog.open(PrepareTrackingNumberComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo
      }
    });
    this.dialogPrepareTrackingRef.afterClosed().subscribe(result => {
      if (result) {
        result.subOrderName = this.orderDetails.subOrderStatus.subOrderName;
        this.onPrepareTrackingNumberEmmit.emit({data: result, orderId: this.orderId});
      }
    });
  }

  onDownloadEmmitFile(): void {
    const data = {
      subOrderId: this.orderDetails.subOrderStatus.subOrderId,
      portable: false
    };
    this.onDownloadFile.emit({data, orderId: this.orderId});
  }
  /**
   * open Upload Files pop-up and emit result data
   * @return void
   */
  onUploadFile(): void {
    this.dialogUploadFileRef = this.dialog.open(UploadFileComponent, {
      maxWidth: '30vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo
      }
    });
    this.dialogUploadFileRef.afterClosed().subscribe(result => {
      if (result) {
        const data = {
          file: result.file,
          userId: Number(localStorage.getItem('userCNId')),
          orderId: this.orderId,
          token: localStorage.getItem('token'),
          scanType: this.orderDetails.subOrderStatus.uploadType,
          subOrderDetails: this.orderDetails.subOrderStatus
        };
        this.onUploadFileEmmit.emit({data, orderId: this.orderId});
      }
    });
  }
  onConfirmSurgicalGuide(): void {
    this.onConfirmingSurgicalGuide.emit({orderDetails: this.orderDetails, orderId: this.orderId});
  }
  /**
   * Upload GFMRF Files pop-up and emit result data
   * @return void
   */
  onUploadGFMRFiles(): void {
    this.dialogUploadGFMRFileRef = this.dialog.open(UploadGFMRFilesComponent, {
      maxWidth: '90vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo
      }
    });
    this.dialogUploadGFMRFileRef.afterClosed().subscribe(result => {
      if (result) {
        const data = {
          lowerSTLFile: result.lowerSTLFile,
          upperSTLFile: result.upperSTLFile,
          biteSTLFile: result.biteSTLFile,
          userId: Number(localStorage.getItem('userCNId')),
          orderId: this.orderId,
          token: localStorage.getItem('token'),
          scanType: this.orderDetails.subOrderStatus.uploadType,
          subOrderDetails: this.orderDetails.subOrderStatus
        };
        this.onUploadGRMRFilesEmmit.emit({data, orderId: this.orderId});
      }
    });
  }
  /**
   * Accept/Reject RestorationGFMR pop-up and emit result data
   * @return void
   */
  onAcceptRejectRestorationGFMR(): void {
    this.dialogAcceptRejectRestorationGFMRRef = this.dialog.open(AcceptRejectRestorationGfmrComponent, {
      maxWidth: '40vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
        orderDetail: this.orderDetails,
        orderId: this.orderId
      }
    });
    this.dialogAcceptRejectRestorationGFMRRef.afterClosed().subscribe(result => {
      if (result) {
        result.statusId = this.orderDetails.statusId;
        result.subOrderId = this.orderDetails.subOrderId;
        result.orderId = this.orderId;
        this.onAcceptRejectRestorationGFMREmmit.emit(result);
      }
    });
  }

  trackSubOrder(): void {
    this.dialogTrackShippedOrder = this.dialog.open(TrackShippedOrderComponent, {
      maxWidth: '60vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
        orderDetails: this.orderDetails
      }
    });
  }
  stringifyUploadOBJ(orderId: number, orderType: string): string {
    return JSON.stringify({id: orderId, type: orderType});
  }
}
