import {Component, Inject, OnInit} from '@angular/core';
import {FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry} from 'ngx-file-drop';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {CStaticIds} from '../../shared-static-id/shared-static-id';
import { UploadProgressService } from 'shared/shared-services/upload-progress.service';
import {OrderListService} from '../../../projects/connect/src/app/Services/OrderServices/OrderListService/order-list.service';
import {SubOrderType} from '../../shared-static-data/SubOrderType';
import {ActionsService} from '../../../projects/connect/src/app/Services/OrderServices/actions/actions.service';

@Component({
  selector: 'app-upload-gfmr-files',
  templateUrl: './upload-gfmrfiles.component.html',
  styleUrls: ['./upload-gfmrfiles.component.css'],
})
export class UploadGFMRFilesComponent implements OnInit {
  public lowerSTLFiles: NgxFileDropEntry[] = [];
  public upperSTLFiles: NgxFileDropEntry[] = [];
  public biteSTLFiles: NgxFileDropEntry[] = [];
  // Array of NgxFileDropEntry containing facial scan and Duchenne smile files.
  public facialScanAndDuchenneSmileFiles: NgxFileDropEntry[] = [];
  // Array of NgxFileDropEntry containing DSD files.
  public dsdFiles: NgxFileDropEntry[] = [];
  lowerSTLFile!: File;
  upperSTLFile!: File;
  biteSTLFile!: File;
  // File containing the facial scan and Duchenne smile data.
  facialScanAndDuchenneSmileFile!: File;
  // File containing the DSD data.
  dsdFile!: File;

  htmlID = CStaticIds;
  isAllowedLowerSTLFile: boolean = false;
  errorLowerSTLFilesMessage: string = '';

  isAllowedUpperSTLFile: boolean = false;
  errorUpperSTLFilesMessage: string = '';

  isAllowedBiteSTLFilesFile: boolean = false;
  errorBiteSTLFilesMessage: string = '';

  // Boolean value indicating whether the facial scan and Duchenne smile file is allowed or not.
  isAllowedfacialScanAndDuchenneSmileFile: boolean = false;
  // A string representing the error encountered while processing facial scan and Duchenne smile file.
  errorfacialScanAndDuchenneSmileFile: string = '';

  // Boolean value indicating whether the DSD file is allowed or not.
  isAllowedDsdFile: boolean = false;
  // A string representing the error encountered while processing DSD file.
  errorDsdFile: string = '';

  constructor(
    public dialogRef: MatDialogRef<UploadGFMRFilesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private uploadProgressService: UploadProgressService,
    private orderService: OrderListService,
    private actionService: ActionsService
  ) {}

  ngOnInit(): void {}

  cancel(): void {
    this.dialogRef.close();
  }

  public droppedLowerSTL(files: NgxFileDropEntry[]): void {
    this.lowerSTLFiles = files;
    for (const droppedFile of files) {
      this.isAllowedLowerSTLFile = this.isFileAllowed(
        droppedFile.fileEntry.name.toLocaleLowerCase()
      ).allowed;
      if (droppedFile.fileEntry.isFile && this.isAllowedLowerSTLFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.lowerSTLFile = file;
          this.actionService.lowerStlFileName =
            this.lowerSTLFiles[0].fileEntry.name;
        });
      } else {
        this.errorLowerSTLFilesMessage = this.isFileAllowed(
          droppedFile.fileEntry.name.toLocaleLowerCase()
        ).message;
      }
    }
  }

  public upperSTL(files: NgxFileDropEntry[]): void {
    this.upperSTLFiles = files;
    for (const droppedFile of files) {
      this.isAllowedUpperSTLFile = this.isFileAllowed(
        droppedFile.fileEntry.name.toLocaleLowerCase()
      ).allowed;
      if (droppedFile.fileEntry.isFile && this.isAllowedUpperSTLFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.upperSTLFile = file;
          this.actionService.upperStlFileName =
            this.upperSTLFiles[0].fileEntry.name;
        });
      } else {
        this.errorBiteSTLFilesMessage = this.isFileAllowed(
          droppedFile.fileEntry.name.toLocaleLowerCase()
        ).message;
      }
    }
  }

  public biteSTL(files: NgxFileDropEntry[]): void {
    this.biteSTLFiles = files;
    for (const droppedFile of files) {
      this.isAllowedBiteSTLFilesFile = this.isFileAllowed(
        droppedFile.fileEntry.name.toLocaleLowerCase()
      ).allowed;
      if (droppedFile.fileEntry.isFile && this.isAllowedBiteSTLFilesFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.biteSTLFile = file;
          this.actionService.biteStlFileName =
            this.biteSTLFiles[0].fileEntry.name;
        });
      } else {
        this.errorUpperSTLFilesMessage = this.isFileAllowed(
          droppedFile.fileEntry.name.toLocaleLowerCase()
        ).message;
      }
    }
  }

  /**
   * Scans and detects a Duchenne smile in the provided files using facial recognition.
   *
   * @param files - An array of NgxFileDropEntry objects representing the files to be scanned.
   */
  public facialScanAndDuchenneSmile(files: NgxFileDropEntry[]): void {
    this.facialScanAndDuchenneSmileFiles = files;
    for (const droppedFile of files) {
      this.isAllowedfacialScanAndDuchenneSmileFile = this.isFileAllowed(
        droppedFile.fileEntry.name.toLocaleLowerCase()
      ).allowed;
      if (
        droppedFile.fileEntry.isFile &&
        this.isAllowedfacialScanAndDuchenneSmileFile
      ) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.facialScanAndDuchenneSmileFile = file;
          this.actionService.facialAndSmileFileName =
            this.facialScanAndDuchenneSmileFiles[0].fileEntry.name;
        });
      } else {
        this.errorfacialScanAndDuchenneSmileFile = this.isFileAllowed(
          droppedFile.fileEntry.name.toLocaleLowerCase()
        ).message;
      }
    }
  }

  /**
   * Scans and detects a dsd in the provided files using facial recognition.
   *
   * @param files - An array of NgxFileDropEntry objects representing the files to be scanned.
   */
  public dsd(files: NgxFileDropEntry[]): void {
    this.dsdFiles = files;
    for (const droppedFile of files) {
      this.isAllowedDsdFile = this.isFileAllowed(
        droppedFile.fileEntry.name.toLocaleLowerCase()
      ).allowed;
      if (droppedFile.fileEntry.isFile && this.isAllowedDsdFile) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.dsdFile = file;
          this.actionService.DSDPhotosFileName =
            this.dsdFiles[0].fileEntry.name;
        });
      } else {
        this.errorDsdFile = this.isFileAllowed(
          droppedFile.fileEntry.name.toLocaleLowerCase()
        ).message;
      }
    }
  }

  isFileAllowed(fileName: string): { allowed: boolean; message: string } {
    let isAllowedFile = false;
    const allowedFiles = ['.zip', '.STL', '.stl'];
    const regex = /(?:\.([^.]+))?$/;
    const extension = regex.exec(fileName);
    if (undefined !== extension && null !== extension) {
      for (const ext of allowedFiles) {
        if (ext === extension[0]) {
          isAllowedFile = true;
        }
      }
    }
    return {
      allowed: isAllowedFile,
      message: isAllowedFile
        ? ''
        : 'File Not Allowed Must Be ' + allowedFiles.map((c) => c),
    };
  }

  onSubmit(): void {
    if (!this.lowerSTLFile && !this.isAllowedLowerSTLFile) {
      return;
    }
    if (!this.upperSTLFile && !this.isAllowedUpperSTLFile) {
      return;
    }
    if (!this.biteSTLFile && !this.isAllowedBiteSTLFilesFile) {
      return;
    }
    if (
      !this.facialScanAndDuchenneSmileFile &&
      !this.isAllowedfacialScanAndDuchenneSmileFile
    ) {
      return;
    }
    if (!this.dsdFile && !this.isAllowedDsdFile) {
      return;
    }
    this.dialogRef.close({
      lowerSTLFile: this.lowerSTLFile,
      upperSTLFile: this.upperSTLFile,
      biteSTLFile: this.biteSTLFile,
      facialScanAndDuchenneSmileFile: this.facialScanAndDuchenneSmileFile,
      dsdFile: this.dsdFile,
    });
  }
}
