import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
  ViewEncapsulation,
} from '@angular/core';
import {
  FormControl,
  FormGroup,
  Validators,
  FormBuilder,
} from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { environment } from 'projects/connect/src/environments/environment';
import { AboutDigitalServiceComponent } from 'shared/shared-components/shard-popup/about-digital-service/about-digital-service.component';
import { digitalDesignServices } from 'shared/shared-static-data/digitalDesignServices';
import { CStaticIds } from 'shared/shared-static-id/shared-static-id';

@Component({
  selector: 'app-restoration-form',
  templateUrl: './restoration-form.component.html',
  styleUrls: ['./restoration-form.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class RestorationFormComponent implements OnInit, OnChanges {
  @Input() restoration!: FormGroup;
  htmlID = CStaticIds;

  /* object of digital design services */
  services: any;

  /* object of keys to the digital design services */
  servicesKeys: any;
/* boolean return true if there are any services selected otherwise it will return false */
  servicesObjValue:boolean=false;
  dialogRef!: MatDialogRef<AboutDigitalServiceComponent, any>;

  activeIndex: number | undefined;

  /**
   *
   * @param dialog
   * @param fb
   */
  constructor(public dialog: MatDialog, private fb: FormBuilder) {}

  ngOnInit(): void {
    this.setServices();
    this.restoration.controls.restType.valueChanges.subscribe((value) => {
      this.setServices();
    });
    }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.restoration) {
      this.setServices();
    }
  }

  /**
   * Sets the services based on the type of restoration selected.
   * Updates the `services` object and `servicesKeys` array accordingly.
   */
  setServices(): void {
    // Get the selected restoration type
    const type = this.restoration.controls.restType.value;

    if (type === 0) {
      // Final
      // Set the services for final restoration type
      this.services = {
        'Custom Implant abutment from Scanning Body including Model': undefined,
        'Implant Crown including Model (From Scan Body)': undefined,
        'Crown including Model (On Prepped Tooth)': undefined,
        'Complete Denture': undefined,
        'Partial Denture Per Tooth including wax-up design': undefined,
        'Final Full Arch Restoration including temp (From Scan Body)':
          undefined,
      };
    } else if (type === 1) {
      // Temp
      // Set the services for temporary restoration type
      this.services = {
        'Custom Implant abutment from Scanning Body including Model': undefined,
        'Custom healing abutment': undefined,
        'Implant Crown including Model (From Scan Body)': undefined,
        'Crown including Model (On Prepped Tooth)': undefined,
        'Complete Denture': undefined,
        'Temporary Partial Dentures Per tooth': undefined,
      };
    } else if (type === 3) {
      // Other
      // Set the services for other restoration type
      this.services = {
        'DSD per Tooth including Model if needed': undefined,
        'Diagnostic Wax up': undefined,
      };
    }

    // Update the keys of the services object
    this.servicesKeys = Object.keys(this.services);
  }

  /**
   * open the about service model
   * @param serviceName : the name of the digital design service which will display in the model
   */
  onOpenAboutService(serviceName: any): void {
    this.dialogRef = this.dialog.open(AboutDigitalServiceComponent, {
      width: '70vw',
      height: '80vh',
      data: {
        logo: environment.connectLogo,
        digitalService: serviceName,
      },
    });
  }

  /**
   * make the service btn active or not and according to that
   * it will add or remove the service form group from the restoration form
   * @param serviceName : the key in the services obj that used as a form group in the restoration form
   */
  onSelectService(serviceName: string) {
    // if the service btn active and now we need to deactivate it
    if (this.services[serviceName]) {
      // remove the service form group from the restoration form group
      this.restoration.removeControl(serviceName);
      // make the btn inactive
      this.services[serviceName] = undefined;
    }
    // if the CrownIncludingModel btn deactivated and now we need to activate it
    else {
      // add the crownIncludingModelDDs form group to the restoration form group
      this.restoration.addControl(serviceName, new FormGroup({}));
      // make the btn active
      this.services[serviceName] = this.restoration.controls[serviceName];
    }
    this.onCheckValues()
  }

  /**
   * check on if the user select any service or no
   * to display or hide the "Expand All" and "Collapse All" box
   */
  onCheckValues(){
    let result;
    result = Object.values(this.services);
    this.servicesObjValue = result.some(item => item != undefined)
  }
}
