import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { TreatmentPlanOrderDialogComponent } from 'shared/shared-components/shard-popup/treatment-plan-order-dialog/treatment-plan-order-dialog.component';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';
import { DoCheck } from '@angular/core';
import {MatRadioChange} from '@angular/material/radio';

@Component({
	selector: 'app-treatment-plan-form',
	templateUrl: './treatment-plan-form.component.html',
	styleUrls: ['./treatment-plan-form.component.css']
})
export class TreatmentPlanFormComponent implements OnInit {
	@Input() treatmentPlan!: FormGroup;
	@Input() surgicalGuide!: FormGroup;
	@Input() buttonOrders: any;
	@Input() moreThan!: boolean;
	@Output() changeValue: EventEmitter<Function> = new EventEmitter<Function>();
	tpReviewPrice: any;
	htmlID = CStaticIds;
  @Input() expressSoftware!: boolean;
	constructor(
		public orderService: OrderSharedService,
		public dialog: MatDialog,
	) { }

	ngOnInit(): void {
		this.tpReviewPrice = localStorage.getItem('tpReviewPrice');
		this.getIsReviewTPValue();

	}

	getIsReviewTPValue(): void {
		this.treatmentPlan.controls.isReviewTP.valueChanges.subscribe(res => {
			if (res) {
        this.orderService.buttons.teethChart = false;
      }
			else this.orderService.buttons.teethChart = true;
		});
	};

	treatmentPlanDialog() {
		this.dialog.open(TreatmentPlanOrderDialogComponent);
	}


  checkSgGuided($event: MatRadioChange): void {
    if (this.orderService.buttons.surgicalGuide) {
      this.surgicalGuide?.controls?.software.patchValue($event.value);
      this.surgicalGuide?.controls?.typeofGuide.patchValue(1);
      this.surgicalGuide?.controls?.guidedSurgeryKitType.patchValue(null);
      this.surgicalGuide?.controls?.guided.patchValue(1);
      this.surgicalGuide?.controls?.intraoralScan.patchValue(1);
    }
  }
}
