import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {CStaticIds} from '../../shared-static-id/shared-static-id';
import {AddOrderService} from '../../../projects/connect/src/app/Services/OrderServices/AddOrderService/add-order.service';
import {ServiceNameEnum} from '../../shared-enums/ServiceNameEnum';
import {OrdersService} from '../../../projects/straumann/src/app/Services/OrdersService/orders.service';
import {OrderListService} from '../../../projects/connect/src/app/Services/OrderServices/OrderListService/order-list.service';
import {ResponseCodeEnum} from '../../shared-enums/ResponseCodeEnum';
import {ERequestTypes} from '../../../projects/connect/src/app/Models/Enums/RequestTypes/RequestTypes';

@Component({
  selector: 'app-accept-reject-restoration-gfmr',
  templateUrl: './accept-reject-restoration-gfmr.component.html',
  styleUrls: ['./accept-reject-restoration-gfmr.component.css']
})
export class AcceptRejectRestorationGfmrComponent implements OnInit {
  htmlID = CStaticIds;
  cost: number | undefined;
  /**
   * The Requirements For The Construction Of The Component
   * @param addOrderService
   * @param ordersService
   * @param dialogRef for dialog reference
   * @param data for dialog data
   */
  constructor(
    private addOrderService: AddOrderService,
    private ordersService: OrderListService,
    public dialogRef: MatDialogRef<AcceptRejectRestorationGfmrComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
    this.getGFMRdetails(this.data.orderId);
  }
  /**
   * close dialog
   * @return void
   */
  cancel(): void {
    this.dialogRef.close();
  }
  /**
   * onSubmit close dialog
   * @return void
   */
  onSubmit(isApprove: boolean): void {
    this.dialogRef.close({isApprove});
  }
  getGFMRdetails(id: number): any{
     this.ordersService.getGfmrData(id).subscribe(res => {
      if (res.responsCode === ResponseCodeEnum.OK) {
        const orderDetails = JSON.parse(res.reponses[0].responsString);
        this.calculateCost(orderDetails);
      }
    });
  }
  calculateCost(order: any): void {
    const gfmrData = {
      columns: [],
      ConditionsAnded: true,
      serviceName: ServiceNameEnum.CostCalculate,
      orderDescription: {
        isPortable: false,
        modelScan: false,
        missingTeeth: [],
        teethToBeExtracted: [],
        implantSites: [],
        restorationData: {
          softwareFormat: [],
          restorationType: 2,
          shadeSys: order.shadeSys,
          shadeValue: order.shadeValue,
          archOfInterest: order.archOfInterest,
          chairSideAssistance: order.chairSideAssistance,
          toBeBilled: order.toBeBilled
        },
        crownSites: [],
        abutmentSites: []
      },
      asc: false,
      pageSize: -1,
      pageNumber: -1,
      sortedByColumnsIndices: []
    };
    const sentData = { services: [gfmrData], requestType: ERequestTypes.VIEW };
    this.addOrderService.calculateCost(sentData).subscribe(res => {
    this.cost = res.restorationCost.cost });
  }
}
