import {Injectable} from '@angular/core';
import {BehaviorSubject, Observable, Subject, Subscription} from 'rxjs';
import {HttpClient, HttpParams, HttpRequest} from '@angular/common/http';
import {environment} from '../../../environments/environment';
import {APIName} from '../../../../../../shared/API/APIName';
import {CaseStatus} from '../../../../../../shared/shared-classes/case-status';
import {IUpload} from '../../Models/Interfaces/Upload/upload';

@Injectable({
  providedIn: 'root'
})
export class UploadService {
  index: number = -1;
  uploadData: Subject<IUpload[]> = new Subject();
  progress: Subject<number> = new Subject();
  calculatedTime: Subject<any> = new Subject();
  subScribe: Subject<Subscription> = new Subject();
  uploadDataNew: any[] = [];
  /**
   * The Requirements For The Construction Of The service
   * @param httpClient for using Http methods
   */
  constructor(private httpClient: HttpClient) {
  }
  /**
   * upload files
   * @param data form data
   * @param userId user id
   * @param orderId order id
   * @param subOrderDetails sub order details
   * @param fileName file name
   * @return Observable<any>
   */
  onUploadFile(data: FormData, userId: number, orderId: number, subOrderDetails: CaseStatus, fileName: string, OrderId2? : number): Observable<any> {

    let params = new HttpParams();
    params = params.append('userID', userId);
    params = params.append('orderID', orderId);
    const req = new HttpRequest('POST', environment.cpRootURL + APIName.uploadFile, data, {
      reportProgress: true,
      responseType: 'json',
      params
    });

    this.uploadDataNew.push({
      userId,
      orderId,
      subOrderDetails,
      fileName,
      showCancel: true,
      progress: this.progress,
      calculatedTime: this.calculatedTime,
      subScribe: this.subScribe,
      OrderId2: OrderId2,
    });
    return this.httpClient.request(req);
  }
  /**
   * get upload data
   * @return Observable<IUpload[]>
   */
  getUploadData(): Observable<IUpload[]> {
    return this.uploadData.asObservable();
  }
  /**
   * set upload data
   * @param uploadData upload data
   * @return void
   */
  setUploadData(uploadData: null | IUpload[]): void {
    if (uploadData != null) {
      this.uploadData.next(uploadData);
    }
  }
}
