import { Directive, EventEmitter, HostBinding, HostListener, Input, Output } from '@angular/core';

@Directive({
  selector: '[appDNDUpload]'
})
export class DragDropFileUploadDirective {

  _done = false

  @Input() appDNDUpload = {default: '#888', light: '#ccc', done:'#333'}
  @Output() dropped = new EventEmitter();


  @HostBinding('style.border-color') private BorderColor = this.appDNDUpload.default;
  @HostBinding('style.color') private Color = this.appDNDUpload.default;
  // Dragover Event
  @HostListener('dragover', ['$event']) dragOver(event: any) {
    event.preventDefault();
    event.stopPropagation();
    this.BorderColor = this.appDNDUpload.light;
    this.Color = this.BorderColor;
  }
  // Dragleave Event
  @HostListener('dragleave', ['$event']) public dragLeave(event: any) {
    event.preventDefault();
    event.stopPropagation();
    this.BorderColor = (this.BorderColor !== '#333')? this.appDNDUpload.done : this.appDNDUpload.default;
    this.Color = this.BorderColor;
  }
  // Drop Event
  @HostListener('drop', ['$event']) public drop(event: any) {
    event.preventDefault();
    event.stopPropagation();
    this.BorderColor = this.appDNDUpload.done;
    this.Color = this.BorderColor;
    const files = event.dataTransfer.files;
    if (files.length > 0) {
      this.dropped.emit(files[0])
      this._done = true;
    }
  }

}
