import {
  Component,
  Input,
  OnInit,
  Output,
  EventEmitter,
  DoCheck,
} from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { RestVitaClassicShades } from 'shared/shared-static-data/restVitaClassicShades';
import { RestVitapanMasterShades } from 'shared/shared-static-data/restVitapanMasterShades';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';
import { ActionsService } from '../../../../../projects/connect/src/app/Services/OrderServices/actions/actions.service';

@Component({
  selector: 'app-gfmr-restoration',
  templateUrl: './gfmr-restoration.component.html',
  styleUrls: ['./gfmr-restoration.component.css'],
})
export class GFMRRestorationComponent implements OnInit {
  @Input() restoration!: FormGroup;
  @Input() orderDetails: any;
  @Output() changeValue: EventEmitter<Function> = new EventEmitter<Function>();
  RestVitaClassic: Array<string> = RestVitaClassicShades;
  RestVitapanMaster: Array<string> = RestVitapanMasterShades;
  retentionType: number = 0;
  abutment: boolean = false;
  tiBaseDisabled: boolean = true;
  shade: number = 1;
  @Output() restType: EventEmitter<number> = new EventEmitter<number>();
  abutmentMaterial!: number;
  htmlID = CStaticIds;

  constructor(
    private fb: FormBuilder,
    public orderService: OrderSharedService,
    private actionService: ActionsService
  ) {}

  ngOnInit(): void {
    this.getRetentionTypeValue();
    this.getShadeValue();
    this.getRestTypeValue();
    this.getAbutmentValue();
  }

  ngDoCheck(): void {
    this.checkOrderArchType();
    this.getRetentionTypeValue();
  }

  /**
   * This function get retention type input value
   * Disabled or enabled retentionTypeSpecify control or radio button Ti base depend on retention type input value
   * Set value to abutment control
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getRetentionTypeValue(): void {
    this.restoration.controls.retentionType?.valueChanges.subscribe(
      (retentionType) => {
        this.retentionType = retentionType;
        if (this.retentionType === 1) {
          this.abutment = true;
          this.restoration.controls.abutment.setValue(4);
        } else {
          this.abutment = false;
          this.restoration.controls.abutment.setValue(0);
        }
        this.retentionType !== 0
          ? (this.tiBaseDisabled = false)
          : (this.tiBaseDisabled = true);
        this.retentionType === 2
          ? this.restoration.controls.retentionTypeSpecify.enable()
          : this.restoration.controls.retentionTypeSpecify.disable();
        this.restoration.controls.retentionTypeSpecify.setValue('');
      }
    );
  }

  /**
   * This function get shade input value
   * Adding or removing control shadeOther or radio button Ti base depend on shade input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getShadeValue(): void {
    this.restoration.controls.shade.valueChanges.subscribe((shade) => {
      this.shade = shade;
      this.shade === 2
        ? this.restoration.addControl(
            'shadeOther',
            this.fb.control('', [Validators.required])
          )
        : this.restoration.removeControl('shadeOther');
    });
  }

  /**
   * This function get rest type input value and output restType control value if value equal 3
   * check buttons and create sub order (treatmentPlan, surgicalGuide) form depend on guided full mouth radio button
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getRestTypeValue(): void {
    this.restoration.controls.restType.valueChanges.subscribe((restType) => {
      if (restType === 2) {
        this.restType.emit(restType);
        this.setFilesRequired();
        this.restoration.removeControl('pouring');
        this.restoration.removeControl('retentionType');
        this.restoration.removeControl('retentionTypeSpecify');
        this.restoration.removeControl('crown');
        this.restoration.removeControl('crownSpecify');
        this.restoration.removeControl('abutment');
        this.restoration.removeControl('abutmentSpecify');
        this.restoration.removeControl('oral');
        this.restoration.removeControl('isRush');
      } else {
        this.unsetFilesRequired();
        this.restoration.addControl(
          'pouring',
          this.fb.control([0, Validators.required])
        );
        this.restoration.addControl(
          'retentionType',
          this.fb.control([0, Validators.required])
        );
        this.restoration.addControl(
          'retentionTypeSpecify',
          this.fb.control({ value: '', disabled: true })
        );
        this.restoration.addControl(
          'crown',
          this.fb.control([1, Validators.required])
        );
        this.restoration.addControl(
          'crownSpecify',
          this.fb.control({ value: '', disabled: true })
        );
        this.restoration.addControl(
          'abutment',
          this.fb.control([0, Validators.required])
        );
        this.restoration.addControl(
          'abutmentSpecify',
          this.fb.control({ value: '', disabled: true })
        );
        this.restoration.addControl(
          'oral',
          this.fb.control([1, Validators.required])
        );
        this.restoration.addControl(
          'isRush',
          this.fb.control([0, Validators.required])
        );
        this.restoration.controls.pouring.setValue(0);
        this.restoration.controls.retentionType.setValue(0);
        this.restoration.controls.crown.setValue(1);
        this.restoration.controls.abutment.setValue(0);
        this.restoration.controls.oral.setValue(1);
        this.restoration.controls.isRush.setValue(0);
      }
    });
  }

  /**
   * This function get abutment input value
   * Disabled or enabled abutmentSpecify control depend on abutment input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getAbutmentValue(): void {
    this.restoration.controls.abutment?.valueChanges.subscribe((abutment) => {
      this.abutmentMaterial = abutment;
      this.abutmentMaterial === 5
        ? this.restoration.controls.abutmentSpecify.enable()
        : this.restoration.controls.abutmentSpecify.disable();
      this.restoration.controls.abutmentSpecify.setValue('');
    });
  }

  checkOrderArchType(): void {
    if (this.orderDetails) {
      if (this.orderDetails[0].OrderArchType === 'Maxilla') {
        // this.restoration.controls.archOfInterest.patchValue(0);
        // this.restoration.get('archOfInterest')?.updateValueAndValidity();
        this.restoration.patchValue({ archOfInterest: 0 });
        this.restoration.get('archOfInterest')?.updateValueAndValidity();
      }
      if (this.orderDetails[0].OrderArchType === 'Mandible') {
        // this.restoration.controls.archOfInterest.patchValue(1);
        // this.restoration.get('archOfInterest')?.updateValueAndValidity();
        this.restoration.patchValue({ archOfInterest: 1 });
        this.restoration.get('archOfInterest')?.updateValueAndValidity();
      }
    }
  }

  GFMRModelScanChange(event: any) {
    if (event.value == 0) {
      this.unsetFilesRequired();
    } else {
      this.setFilesRequired();
    }
  }
  GFMRDSDPhotosChange(event: any) {
    if (event.value == 0) {
      this.restoration.controls.DSDphotosFiles.patchValue(null);
      this.restoration.controls.DSDphotosFiles.setValidators(null);
      this.restoration.controls.DSDphotosFiles.updateValueAndValidity();
    } else {
      this.restoration.controls.DSDphotosFiles.setValidators([
        Validators.required,
      ]);
      this.restoration.controls.DSDphotosFiles.updateValueAndValidity();
    }
  }
  setFilesRequired() {
    this.restoration.controls.lowerFile.setValidators([Validators.required]);
    this.restoration.controls.lowerFile.updateValueAndValidity();

    this.restoration.controls.upperFile.setValidators([Validators.required]);
    this.restoration.controls.upperFile.updateValueAndValidity();

    this.restoration.controls.biteFile.setValidators([Validators.required]);
    this.restoration.controls.biteFile.updateValueAndValidity();

    this.restoration.controls.FacialScan.setValidators([Validators.required]);
    this.restoration.controls.FacialScan.updateValueAndValidity();
  
  }

  unsetFilesRequired() {
    this.restoration.controls.lowerFile.patchValue(null);
    this.restoration.controls.lowerFile.setValidators(null);
    this.restoration.controls.lowerFile.updateValueAndValidity();
    this.restoration.controls.upperFile.patchValue(null);
    this.restoration.controls.upperFile.setValidators(null);
    this.restoration.controls.upperFile.updateValueAndValidity();
    this.restoration.controls.biteFile.patchValue(null);
    this.restoration.controls.biteFile.setValidators(null);
    this.restoration.controls.biteFile.updateValueAndValidity();
    this.restoration.controls.FacialScan.patchValue(null);
    this.restoration.controls.FacialScan.setValidators(null);
    this.restoration.controls.FacialScan.updateValueAndValidity();
  
  }

  onFileSelected(files: any, key: string) {
    const file = files[0];

    if (!file) {
      return;
    }
    if (key == 'lower') {
      this.restoration.patchValue({ lowerFile: file });
      this.actionService.lowerStlFileName = file.name;
    }
    if (key == 'upper') {
      this.restoration.patchValue({ upperFile: file });
      this.actionService.upperStlFileName = file.name;
    }
    if (key == 'bite') {
      this.restoration.patchValue({ biteFile: file });
      this.actionService.biteStlFileName = file.name;
    }
    if (key == 'FacialScan') {
      this.restoration.patchValue({ FacialScanFile: file });
      this.actionService.facialAndSmileFileName = file.name;
    }
    if (key == 'DSDPhotos') {
      this.restoration.patchValue({ DSDphotosFiles: file });
      this.actionService.DSDPhotosFileName = file.name;
    }
  }

  /*
   *  Removes the 'printerGroup' and 'deliverablesgroup' form controls from the surgicalGuide FormGroup
   *  when the 'No Digital Design' option is selected.
   */
  onSelectNoDigitalDesign() {
    this.restoration.removeControl('printerGroup');
    this.restoration.removeControl('deliverablesgroup');
   
  }

  onSelectDigitalDesign() {
    this.restoration.addControl('printerGroup', new FormGroup({}));
    this.restoration.addControl('deliverablesgroup', new FormGroup({}));
    
  }
}
