import { Injectable } from '@angular/core';
import { DataService } from '../../DataService/data.service';
import { APIName } from '../../../../../../../shared/API/APIName';
import { Observable } from 'rxjs';
import { IMainResponse } from '../../../Models/Interfaces/Main/MainResponse';
import { ERequestTypes } from '../../../Models/Enums/RequestTypes/RequestTypes';
import { IMain } from '../../../Models/Interfaces/Main/Main';
import { IPrintReceiptGet } from '../../../Models/Interfaces/Billing/PrintReceipt/PrintReceipt';

@Injectable({
    providedIn: 'root'
})
export class PrintReceiptService {
    printReceiptOrder = { services: [] as IPrintReceiptGet[] } as IMain;
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
    constructor(private dataService: DataService) { }
  /**
   * get receipt data
   * @return Observable<IMainResponse>
   */
    getPrintReceiptService(): Observable<IMainResponse> {
        this.printReceiptOrder.requestType = ERequestTypes.VIEW;
        return this.dataService.post(APIName.ORDERS, this.printReceiptOrder);
    }
}
