import { AbstractControl, AsyncValidatorFn, FormControl, ValidatorFn } from '@angular/forms';
import { of } from 'rxjs';
import { delay, map } from 'rxjs/operators';

export interface ValidationResult {
  [key: string]: boolean;
}

export class PasswordValidators {
  public static hasUpperAsync(): AsyncValidatorFn  {
    return (control: AbstractControl) => {
      return of(control.value).pipe(
        delay(500),
        map((value) => /[A-Z]/.test(value)? null: { upper: true })
      )
      // return control.pristine || /[A-Z]/.test(control.value)
      //   ? null
      //   : { upper: true };
    };
  }

  public static hasUpper(): ValidatorFn {
    return (control: AbstractControl): ValidationResult | null => {
      return control.pristine || /[A-Z]/.test(control.value)
        ? null
        : { upper: true };
    };
  }

  public static hasDigit(): ValidatorFn {
    return (control: AbstractControl): ValidationResult | null => {
      return control.pristine || /\d/.test(control.value)
        ? null
        : { digit: true };
    };
  }

  public static hasLower(): ValidatorFn {
    return (control: AbstractControl): ValidationResult | null => {
      return control.pristine || /[a-z]/.test(control.value)
        ? null
        : { lower: true };
    };
  }

  public static hasSpecialChar(): ValidatorFn {
    return (control: AbstractControl): ValidationResult | null => {
      return control.pristine || /[!@#$%^&*(),.?":{}\/\\|<>]/.test(control.value)
        ? null
        : { special: true };
    };
  }

  public static EqualTo(control1: AbstractControl) {
    return function (control: AbstractControl): ValidationResult | null {
      return control.pristine || control.value === control1.value
        ? null
        : { missMatch: true };
    };
  }
}
