import {Component, OnInit, Output, EventEmitter, Input} from '@angular/core';
import {FormArray, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {MyErrorStateMatcher} from 'shared/shared-functions/state-matcher';
import {ISharedUserTypes} from 'shared/shared-interfaces/user-types';
import {UserTypesData} from 'shared/shared-static-data/userType';
import {PhoneTypes} from 'shared/shared-static-data/phoneTypes';
import {ISharedLeadSourceTypes} from 'shared/shared-interfaces/lead-source-types';
import {Months} from 'shared/shared-static-data/months';
import {DateOfYearService} from 'shared/shared-services/date-of-year.service';
import {ISharedRegister} from 'shared/shared-interfaces/register';
import {ISharedPaymentTypes} from 'shared/shared-interfaces/payment-types';
import {PaymentTypes} from 'shared/shared-static-data/paymentTypes';
import {LeadSourceTypes} from 'shared/shared-static-data/leadSourceTypes';
import {ISharedCountry} from 'shared/shared-interfaces/country';
import {Countries} from 'shared/shared-static-data/Countries';
import {ISharedStates} from 'shared/shared-interfaces/states';
import {ISharedEmailTypes} from 'shared/shared-interfaces/email-type';
import {EmailTypes} from 'shared/shared-static-data/EmailTypes';
import {AddressType} from 'shared/shared-static-data/addressType';
import {ISharedAdressTypes} from 'shared/shared-interfaces/address-types';
import {ISharedPhoneTypes} from 'shared/shared-interfaces/phone-types';
import {States} from 'shared/shared-static-data/States';
import {CStaticIds} from '../../../../shared-static-id/shared-static-id';

/**
 * Copyright (c) 3ddx
 *
 * @summary Register Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {
  registerInfoFormGroup!: FormGroup;
  personalInfoFormGroup!: FormGroup;
  billingInfoFormGroup!: FormGroup;
  matcher!: MyErrorStateMatcher;
  registerIsValid!: boolean;
  isEditable: boolean = true;
  hide: boolean = true;
  submitted: boolean = false;
  UserTypes: Array<ISharedUserTypes> = UserTypesData;
  AddressType: Array<ISharedAdressTypes> = AddressType;
  PhoneTypes: Array<ISharedPhoneTypes> = PhoneTypes;
  Emailtypes: Array<ISharedEmailTypes> = EmailTypes;
  LeadSourceTypes: Array<ISharedLeadSourceTypes> = LeadSourceTypes;
  PaymentTypes: Array<ISharedPaymentTypes> = PaymentTypes;
  Months: Array<string> = Months;
  Countries: Array<ISharedCountry> = Countries;
  States: Array<ISharedStates> = States;
  payment: string = '/assets/Images/Visa.gif';
  Years!: Array<number>;
  @Output() registerInOBJ: EventEmitter<ISharedRegister> = new EventEmitter<ISharedRegister>();
  @Input() straumannForm!: boolean;
  @Input() settingFormData: any;
  htmlID = CStaticIds;
  paymentType!: number;
  countryId!: number;
  /**
   * The Requirements For The Construction Of The Component
   * @param fb for using FormBuilder
   * @param dateOfYearService for using DateOfYearService
   */
  constructor(
    private fb: FormBuilder,
    private dateOfYearService: DateOfYearService,
  ) {
  }

  /**
   * creat phone form group
   * @return FormGroup
   */
  createPhone(e?: any): FormGroup {
    return this.fb.group({
      telecom: e ? e.telecom : '',
      telecomEnumId: e ? e.telecomEnumId : ''
    });
  }
  /**
   * initialize Phones
   * @return FormGroup[]
   */

  initailizePhones(): FormGroup[] {
    const phonesForms: FormGroup[] = [];
    this.settingFormData.telecoms.forEach((e: any) => {
      phonesForms.push(this.createPhone(e));
    });
    return phonesForms;
  }
  /**
   * creat Emails form group
   * @return FormGroup
   */
  CreatEmails(e?: any): FormGroup {
    return this.fb.group({
      email: [e ? e.email : '', [Validators.required, Validators.email]],
      emailEnumId: [e ? e.emailEnumId : '', Validators.required]
    });
  }
  /**
   * creat address form group
   * @return FormGroup
   */
  CreatAdress(e?: any): FormGroup {
    return this.fb.group({
      addressLine1: [e ? e.addressLine1 : null, Validators.required],
      addressLine2: e ? e.addressLine2 : '',
      usersCNAdressesEnumId: [e ? e.usersCNAdressesEnumId : null, Validators.required],
    });
  }
  /**
   * initialize Emails
   * @return FormGroup[]
   */
  initializeemails(): FormGroup[] {
    const emailsForms: FormGroup[] = [];
    this.settingFormData.emails.forEach((e: any) => {
      emailsForms.push(this.CreatEmails(e));
    });
    return emailsForms;
  }

  ngOnInit(): void {
    this.Years = this.dateOfYearService.generateYearsList(20);
    this.createRegisterInfoForm();
    this.createPersonalInfoForm();
    this.createBillingInfoForm();
    this.personalInfoFormGroup.patchValue(this.settingFormData);
    this.registerInfoFormGroup.patchValue(this.settingFormData);
    this.billingInfoFormGroup.patchValue(this.settingFormData);
    this.countryId = this.settingFormData.countryEnumId;
    this.getPaymentType();
    this.getCountryEnumId();
    this.disabledCredit(this.settingFormData.creditCard[0].creditCardEnumId);
    this.matcher = new MyErrorStateMatcher();
    if (!this.settingFormData.creditCard[0].creditCard) {
      this.billingInfoFormGroup.controls.month.addValidators([Validators.required]);
      this.billingInfoFormGroup.controls.year.addValidators([Validators.required]);
      this.billingInfoFormGroup.controls.creditCardNum.addValidators([Validators.required, Validators.minLength(16)]);
    }
  }

  /**
   * create register info form
   * @return void
   */
  createRegisterInfoForm(): void {
    this.registerInfoFormGroup = this.fb.group({
      userType: [this.settingFormData.userCNEnumId, Validators.required],
      Emails: this.fb.array(this.initializeemails())
    });
  }

  /**
   * This function creat personal info form group
   *
   * @return void
   */
  createPersonalInfoForm(): void {
    this.personalInfoFormGroup = this.fb.group({
      portalVersion: [this.settingFormData.isPortableVersion, Validators.required],
      fName: ['', Validators.required],
      lName: ['', Validators.required],
      address: this.fb.array([]),
      city: [''],
      countryEnumId: [''],
      stateEnumId: [''],
      zipCode: [''],
      phones: this.fb.array(this.initailizePhones())
    });
    this.addAddress();
  }

  /**
   * This function creat billing info form group
   *
   * @return void
   */
  createBillingInfoForm(): void {
    this.billingInfoFormGroup = this.fb.group({
      paymentType: [this.settingFormData.creditCard[0].creditCardEnumId],
      creditCardNum: [''],
      month: [''],
      year: [''],
      rcivMail: false
    });
  }

  /**
   * This function return phones control from personalInfoFormGroup group
   *
   * @return void
   */
  get phones(): FormArray {
    return this.personalInfoFormGroup.get('phones') as FormArray;
  }

  get Emails(): FormArray {
    return this.registerInfoFormGroup.get('Emails') as FormArray;
  }

  get address(): FormArray {
    return this.personalInfoFormGroup.get('address') as FormArray;
  }

  /**
   * This function push phone group to phones array
   *
   * @return void
   */
  addPhones(): void {
    this.phones.push(this.createPhone());
  }
  /**
   * This function push emails group to phones array
   *
   * @return void
   */
  addEmails(): void {
    this.Emails.push(this.CreatEmails());
  }
  /**
   * This function push address group to phones array
   *
   * @return void
   */
  addAddress(): void {
    this.settingFormData.addresses.forEach((e: any) => {
      this.address.push(this.CreatAdress(e));
    });
  }

  /**
   * This function remove phone group by index
   * @param index index
   * @return void
   */
  removePhones(index: number): void {
    this.phones.removeAt(index);
  }
  /**
   * This function remove emails group by index
   * @param i index
   * @return void
   */
  removeEmails(i: number): void {
    this.Emails.removeAt(i);
  }
  /**
   * get payment type
   * @return void
   */
  getPaymentType(): void {
    this.billingInfoFormGroup.controls.paymentType.valueChanges.subscribe(paymentType => {
      this.paymentType = paymentType;
      this.disabledCredit(this.paymentType);
    });
  }
disabledCredit(paymentType: number): void {
  if (paymentType === 1000) {
    this.billingInfoFormGroup.controls.creditCardNum.disable();
    this.billingInfoFormGroup.controls.month.disable();
    this.billingInfoFormGroup.controls.year.disable();
    this.billingInfoFormGroup.controls.creditCardNum.setValue('');
    this.billingInfoFormGroup.controls.month.setValue('');
    this.billingInfoFormGroup.controls.year.setValue('');
  } else {
    this.billingInfoFormGroup.controls.creditCardNum.enable();
    this.billingInfoFormGroup.controls.month.enable();
    this.billingInfoFormGroup.controls.year.enable();
  }
}
  getCountryEnumId(): void {
    this.personalInfoFormGroup.controls.countryEnumId.valueChanges.subscribe(res => {
      this.countryId = res;
    });
  };


  /**
   * This function submit form and output data to sign up component
   * @return void
   */
  onSubmit(): void {
    this.submitted = true;
    this.registerInfoFormGroup.addControl('userCNId', new FormControl(this.settingFormData.userCNId));
    this.registerIsValid = (
      this.registerInfoFormGroup.valid &&
      this.personalInfoFormGroup.valid &&
      this.billingInfoFormGroup.valid
    );
    if (this.registerIsValid && this.billingInfoFormGroup.controls.creditCardNum) {
      this.registerInOBJ.emit({
        ...this.registerInfoFormGroup.value,
        ...this.personalInfoFormGroup.value,
        ...this.billingInfoFormGroup.value
      });
    }
    else {
      if (this.registerIsValid && this.settingFormData.creditCard[0].creditCard) {
        this.registerInOBJ.emit({
          ...this.registerInfoFormGroup.value,
          ...this.personalInfoFormGroup.value,
        });
      }
    }
  }

  validateCreditCard(e: any): void {
      this.billingInfoFormGroup.controls.creditCardNum.patchValue('');
      e.value === 1002 ?  this.billingInfoFormGroup.controls.creditCardNum.setValidators([Validators.required , Validators.minLength(15)]) :  this.billingInfoFormGroup.controls.creditCardNum.setValidators([Validators.required, Validators.minLength(16)]);
  }
}
