import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

@Component({
	selector: 'app-note-must-have-credit-card',
	templateUrl: './note-must-have-credit-card.component.html',
	styleUrls: ['./note-must-have-credit-card.component.css']
})
export class NoteMustHaveCreditCardComponent implements OnInit {
  htmlID = CStaticIds;
	constructor(
		public dialogRef: MatDialogRef<NoteMustHaveCreditCardComponent>,
	) { }

	ngOnInit(): void {
	}

	ok() {
		this.dialogRef.close({ event: 'Ok' });
	};

	close() {
		this.dialogRef.close({ event: 'Cancel' });
	};

}
