import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {IGetSurgicalGuide} from '../../../../projects/connect/src/app/Models/Interfaces/Order/SurgicalGuide/get-surgical-guide';
import {SoftwareType} from '../../../shared-static-data/software';
import {GuideSupport} from '../../../shared-static-data/guideSupport';
import {GuideType} from '../../../shared-static-data/guideType';
import {DateService} from '../../../shared-services/date.service';
import {SGExtraComponents} from '../../../shared-static-data/SGExtraComponents';
import {environment} from '../../../../projects/connect/src/environments/environment';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-surgical-guide-confirmation',
  templateUrl: './surgical-guide-confirmation.component.html',
  styleUrls: ['./surgical-guide-confirmation.component.css']
})
export class SurgicalGuideConfirmationComponent implements OnInit {
  logo = 'assets/Images/connectLogo.png';
  notes = {
    additionalComments: ''
  };
  surgicalGuide = {} as IGetSurgicalGuide;
  hostName = window.location.hostname;
  // @ts-ignore
  showboneSupportNote: boolean = false;
  htmlID = CStaticIds;

  /**
   * The Requirements For The Construction Of The Component
   * @param {MatDialogRef<SurgicalGuideConfirmationComponent>} dialogRef For The Dialogue Reference
   * @param data
   * @param dateService
   */
  constructor(
    public dialogRef: MatDialogRef<SurgicalGuideConfirmationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dateService: DateService) {
  }

  ngOnInit(): void {
    this.surgicalGuide = this.data.surgicalGuide;
  }


  getSoftware(): string {
    return Object.values(SoftwareType)
      .filter(softwareType => softwareType.value === this.surgicalGuide.softwareEnum)[0]?.name;
  }

  getGuideSupport(): string {
    this.showboneSupportNote =
      Object.values(GuideSupport).filter(guideSupp => guideSupp.value === this.surgicalGuide.guideSupportEnum)[0]?.value
      === GuideSupport.BONE.value;
    return Object.values(GuideSupport).filter(guideSupp => guideSupp.value === this.surgicalGuide.guideSupportEnum)[0]?.name;
  }

  getGuideType(): string {
    return Object.values(GuideType).filter(guType => guType.value === this.surgicalGuide.guideTypeEnum)[0]?.name;
  }

  getExtraComponents(): any[] {
    const arr: any[] = [];
    if (this.surgicalGuide.extraComponents) {
      this.surgicalGuide?.extraComponents.forEach(component => {
        const obj = {
          name: Object.values(SGExtraComponents).filter(x => x.value === component.id)[0]?.name,
          comment: component.comment,
          cost: this.surgicalGuide.currencySymbol + ' ' + component.cost
        };
        arr.push(obj);
      });
    }
    return arr;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  getConfirmationLink(): string {
    let confirmationLink = '';
    if (this.data.orderId && this.surgicalGuide.sgID && this.surgicalGuide.confirmationToken && this.surgicalGuide.clientID) {
      confirmationLink =
        `${environment.cpRootURL}newcp/GuideConfirmation/?oID=${this.data.orderId}&sgID=${this.surgicalGuide.sgID}&cID=${this.surgicalGuide.clientID}&token=${this.surgicalGuide.confirmationToken}`;
    }
    return confirmationLink;
  }

  onSubmit(type: string): void {
    this.dialogRef.close({
      type
    });
  }
}
