import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormGroup, FormBuilder, Validators} from '@angular/forms';
import {Countries} from 'shared/shared-static-data/Countries';
import {States} from 'shared/shared-static-data/States';
import {ShippingLabelUserData} from '../../../../projects/connect/src/app/Models/Interfaces/ShippingLabel/ShippingLabelUserData';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

/**
 * Copyright (c) 3ddx
 *
 * @summary Shipping Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-shipping-forms',
  templateUrl: './shipping-forms.component.html',
  styleUrls: ['./shipping-forms.component.css']
})
export class ShippingFormsComponent implements OnInit {
  ShippingForm!: FormGroup;
  country = Countries;
  states = States;
  @Output() shippingInOBJ: EventEmitter<any> = new EventEmitter<any>();
  @Input() shippingLabelUserData = {} as ShippingLabelUserData;
  @Input() orderId!: number;
  @Output() downloadFile: EventEmitter<any> = new EventEmitter<any>();
  htmlID = CStaticIds;

  /**
   * The Requirements For The Construction Of The Component
   * @param fb for using FormBuilder
   */
  constructor(private fb: FormBuilder) {
  }

  ngOnInit(): void {
    this.createShippingForm();
  }

  /**
   * This function creat shipping form group
   *
   * @return void
   */
  createShippingForm(): void {
    this.ShippingForm = this.fb.group({
      fullName: [this.shippingLabelUserData.fName, Validators.required],
      email: [this.shippingLabelUserData.email, [Validators.required, Validators.email]],
      country: [239, Validators.required],
      city: [this.shippingLabelUserData.city, Validators.required],
      state: [this.shippingLabelUserData.stateEnumId, Validators.required],
      address: [this.shippingLabelUserData.address1, Validators.required],
      zipCode: [this.shippingLabelUserData.zipCode, Validators.required],
      phone: [this.shippingLabelUserData.phone1, Validators.required],
      timeToShip: ['0', Validators.required]
    });
  }

  /**
   * This function submit form and output data
   * @return void
   */
  submit(): void {
    if (this.ShippingForm.invalid) {
      return;
    } else {
      this.shippingInOBJ.emit(this.ShippingForm.value);
    }
  }

  onDownloadFile(): void {
    this.downloadFile.emit();
  }
}
