import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})

export class DateOfYearService {
  years: number[] = [];
  currentYear!: number;

  constructor() {

  }

  generateYearsList(yearsLimit: number): number[] {
    this.currentYear = new Date().getFullYear();
    for (let i = 0; i < yearsLimit; i++) {
      this.years.push(this.currentYear++);
    }
    return this.years;
  }
}
