import {
  ControlContainer,
  FormControl,
  FormGroup,
} from '@angular/forms';
import { Component, OnInit, Input } from '@angular/core';
import { CStaticIds } from 'shared/shared-static-id/shared-static-id';
import { softwareType } from 'shared/shared-static-data/digitalDesignsSoftware';

@Component({
  selector: 'app-custom-deliver-as',
  templateUrl: './custom-deliver-as.component.html',
  styleUrls: ['./custom-deliver-as.component.css'],
})
export class CustomDeliverAsComponent implements OnInit {
  /*the width of the label by colms */
  @Input() labelWidth!:string;

  /*the width of the checkboxes wrapper by colms */
  @Input() checkBoxes!:string;
  @Input() extraPadding!:string;


  // deliver as form group
  deliverAsFormGroup: any;
  htmlID = CStaticIds;
  /** Software files types. */
  softwareTypeFiles = softwareType;

  constructor(public controlContainer: ControlContainer) {}

  ngOnInit(): void {
    // to make it custom form group
    this.deliverAsFormGroup = this.controlContainer.control;
    this.createFormGroup('stl', true);
    this.createFormGroup('softwareFile', false);
    this.createFormGroup('softwareDesign', '');
    this.deliverAsFormGroup.addValidators(this.validator);
  }
  /**
   *
   * @param controlName :string
   * @param value
   */
  createFormGroup(controlName: string, value: any) {
    this.deliverAsFormGroup.addControl(controlName, new FormControl(value));
  }

  /**
   *
   * @param formGroup
   */
  validator(formGroup: FormGroup): { [key: string]: boolean } | null {
    const stlControl = formGroup.controls['stl'];
    const softwareFileControl = formGroup.controls['softwareFile'];
    const softwareDesignControl = formGroup.controls['softwareDesign'];
    if (!stlControl.value && !softwareFileControl.value) {
      return {
        atLeastOne: true,
      };
    } else if (softwareFileControl.value && !softwareDesignControl.value) {
      return {
        selectSoftware: true,
      };
    }
    return null;
  }
}
