import {Injectable} from '@angular/core';
import {NgxSpinnerService} from 'ngx-spinner';

@Injectable({
  providedIn: 'root'
})
export class LoaderService {
  /** keep record of the current open requests */
  _reqests = 0;

  /** auto hide the spinner afer 15 sec from the last call */
  _timeoutId: number | undefined;

  constructor(private spinner: NgxSpinnerService) {
  }

  show(): void {
    this.spinner.show();
    this._reqests += 1;
    if(this._timeoutId){
      clearTimeout(this._timeoutId);
    }
    setTimeout(() => this.spinner.hide(), 20000)
  }

  hide(): void {
    this._reqests -= 1;
    if(this._reqests <= 0){
      this.spinner.hide();
      this._reqests = 0;
      if(this._timeoutId){
        clearTimeout(this._timeoutId);
      }
    }
  }
}
