import { Injectable } from '@angular/core';
import { EOrderBackgroundColorStatus } from '../shared-enums/order-background-color-status';
import { IOrderListDataDetails } from '../../projects/connect/src/app/Models/Interfaces/Order/OrderList/OrderListDataDetails';
import { GenerateCaseStatus } from '../shared-classes/generate-case-status';
import { DateService } from './date.service';
import { EOrderTextColorStatus } from '../shared-enums/order-text-color-status';


@Injectable({
  providedIn: 'root'
})
export class GenerateOrderDataService {
  constructor(private dateService: DateService) {
  }

  onGenerateArr(orderList: any): any[] {
    const arr: any[] = [];
    orderList?.forEach((order: any) => {
      const orderNewOBJ = {
        notes: this.filterNotes(order.ordersCommonDetails),
        isOwner: this.isOwner(order.scanningCenterId, order.doctorId),
        orderId: order.orderId,
        arch: order.OrderArchType,
        Maxilla: order.maxillaSegmentationEnum,
        Mandible: order.mandibleSegmentationEnum,
        typeofArch: order.OrderArchType === 'Maxilla' ? order.maxillaSegmentationEnum : order.mandibleSegmentationEnum,
        archType: order.OrderArchType === 'Maxilla' ? order.maxillaEnum : order.mandibleEnum,
        software: order.isExpress ? '3DDX Express' : order.softwareEnum,
        client: order.patientName === '--' ? order.scanningCenterFirstName + order.scanningCenterLastName : order.patientName,
        patientName: order.patientName,
        scanningCenter: this.getScanCenter(order),
        doctorName: this.getDoctor(order),
        orderStatus: this.getOrderStatus(this.onGenerateArrSubOrders(order.ordersCommonDetails,
          this.getOrderType(order.ordersCommonDetails), this.isOwner(order.scanningCenterId, order.doctorId))),
        totalCost: [...new Set(order.ordersCommonDetails.map(
          (c: any) => c.currencyEnum))][0] + ' ' + order.ordersCommonDetails.reduce((a: any, b: any) => {
            return a + b.orderEntityCost;
          }, 0),
        subOrders: this.onGenerateArrSubOrders(order.ordersCommonDetails,
          this.getOrderType(order.ordersCommonDetails),
          this.isOwner(order.scanningCenterId, order.doctorId)),
        orderType: this.getOrderType(order.ordersCommonDetails),
        formatEnum: order.formatEnum,

        radiologist: order.operatorFirstNameReport,
        checkAddToOrder: this.checkAddToOrder(order.ordersCommonDetails),
        diffVoucherCost: order.ordersCommonDetails[0].voucherCredit,
        hasDeductedCost: this.hasVoucher(order.ordersCommonDetails),
        deductedTotalCost: [...new Set(order.ordersCommonDetails?.map(
          (c: any) => c.currencyEnum))][0] + order.ordersCommonDetails?.reduce((a: any, b: any) => {
            return a + (b.commonOrderVoucherHistory ? +b.commonOrderVoucherHistory[0]?.amountDeducted : 0);
          }, 0),
      };
      arr.push(orderNewOBJ);
    });
    return arr;
  }

  onGenerateArrUpload(orderList: any): any[] {
    const arr: any[] = [];
    orderList?.forEach((order: any) => {
      const orderNewOBJ = {
        isOwner: this.isOwner(order.scanningCenterId, order.doctorId),
        orderId: order.orderId,
        arch: order.OrderArchType,
        software: order.softwareEnum,
        client: order.patientName === '--' ? order.scanningCenterFirstName + order.scanningCenterLastName : order.patientName,
        patientName: order.patientName,
        scanningCenter: this.getScanCenter(order),
        doctorName: this.getDoctor(order),
        orderStatus: this.getOrderStatusUpload(this.onGenerateArrSubOrders(order.ordersCommonDetails,
          this.getOrderType(order.ordersCommonDetails), this.isOwner(order.scanningCenterId, order.doctorId))),
        totalCost: [...new Set(order.ordersCommonDetails.map(
          (c: any) => c.currencyEnum))][0] + ' ' + order.ordersCommonDetails.reduce((a: any, b: any) => {
            return a + b.orderEntityCost;
          }, 0),
        subOrders: this.onGenerateArrSubOrders(order.ordersCommonDetails,
          this.getOrderType(order.ordersCommonDetails),
          this.isOwner(order.scanningCenterId, order.doctorId)),
        orderType: this.getOrderType(order.ordersCommonDetails),
        formatEnum: order.formatEnum,
      };
      arr.push(orderNewOBJ);
    });
    return arr;
  }

  getScanCenter(order: any): string {
    let scanCenter = '';
    if (order.scanningCenterFirstName || order.scanningCenterLastName) {
      scanCenter = order.scanningCenterFirstName;
      scanCenter += order.scanningCenterLastName ? ' ' + order.scanningCenterLastName : ' ';
    }
    return scanCenter;
  }

  // getCreatedTime(order: any): any {
  //   return this.dateService.dateToEST(order.sort((a: any, b: any) => a.sentOn - b.sentOn)[0].timeSent);
  // }
  //
  // getLastUpdate(order: any): any {
  //   let lastUpdate = order.sort((a: any, b: any) => b.timeEnd - a.timeEnd)[0].timeEnd;
  //   // tslint:disable-next-line:max-line-length
  //   lastUpdate === 0 ? lastUpdate = 'No update' : lastUpdate = this.dateService.dateToEST(lastUpdate);
  //   return lastUpdate;
  // }

  checkAddToOrder(subOrders: any[]): boolean {
    const checkSgReport = subOrders.filter(subOrder =>
      subOrder.orderEnum === 'Surgical Guide' || subOrder.orderEnum === 'Report'
    );
    return !(checkSgReport.length === 2);
  }

  getDoctor(order: any): string {
    let doctor = '';
    if (order.doctorFirstName || order.doctorLastName) {
      doctor = order.doctorFirstName;
      doctor += order.doctorLastName ? ' ' + order.doctorLastName : ' ';
    }
    return doctor;
  }

  filterNotes(ordersCommonDetails: IOrderListDataDetails[]) {
    const notes = ordersCommonDetails.filter((res: any) => {
      if(res.pendingDescription) return res.pendingDescription
    });
    return notes[0]?.pendingDescription;
  }

  isOwner(scanningCenterId: number, doctorId: number): boolean {
    const userId = Number(localStorage.getItem('userCNId'));
    return userId === scanningCenterId || userId === doctorId;
  }

  getOrderStatus(subOrders: any): any {
    const arr = [];
    const sub = subOrders.map((order: any) => order.subOrderStatus);
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.RED)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.RED));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.ORANGE)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.ORANGE));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.YELLOW)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.YELLOW));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.BLUE)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.BLUE));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.GREEN)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.GREEN));
    }
    return arr[0];
  }

  getOrderStatusUpload(subOrders: any): any {
    const arr = [];
    let sub = subOrders.map((order: any) => order.subOrderStatus);
    sub = sub.filter((res: any) => res.uploadType === 'DICOM');
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.RED)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.RED));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.ORANGE)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.ORANGE));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.YELLOW)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.YELLOW));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.BLUE)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.BLUE));
    }
    if (sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.GREEN)) {
      arr.push(sub.find((c: any) => c.bgColor === EOrderBackgroundColorStatus.GREEN));
    }
    return arr[0];
  }

  getOrderType(ordersCommonDetails: IOrderListDataDetails[]): string {
    if (ordersCommonDetails[0].orderEnum.toLowerCase() === 'misc') {
      return 'Misc';
    } else if (ordersCommonDetails[0].orderEnum.toLowerCase() === 'software') {
      return 'Software';
    } else {
      return 'Patient';
    }
  }

  onGenerateArrSubOrders(ordersCommonDetails: IOrderListDataDetails[], orderType: string, isOwner: boolean): any[] {
    const arr: any[] = [];
    ordersCommonDetails.forEach(order => {
      const ordersCommonDetailsNewOBJ = {
        isOwner,
        countOfSubOrders: ordersCommonDetails.length,
        subOrderType: order.orderEnum,
        cost: order.currencyEnum + ' ' + order.orderEntityCost,
        sentOn: this.dateService.dateToEST(order.timeSent),
        notes: order.pendingDescription || null,
        receivedOn: order.timeEnd === 0 ? 'Not Yet' : this.dateService.dateToEST(order.timeEnd),
        updatedOn: order.timeUpdate === 0 ? 'Not Yet' : this.dateService.dateToEST(order.timeUpdate),
        chargedOn: order.timeBilled === 0 ? 'Not Yet' : this.dateService.dateToEST(order.timeBilled),
        statusId: order.statusId,
        subOrderId: order.orderCommonId,
        subOrderStatus: this.getSubOrderStatus(order.statusId, orderType, order, order.timeBilled, ordersCommonDetails, isOwner),
        trackNumber: order.trackNumber ? order.trackNumber : null,
        serviceProvider: order.trackNumber ? order.serviceProvider : null,
        comment: order.userCNComment,
        deductedCost: order.commonOrderVoucherHistory ? order.currencyEnum + ' ' + order.commonOrderVoucherHistory[0]?.amountDeducted : null,
        voucherId: order.commonOrderVoucherHistory ? order.commonOrderVoucherHistory[0]?.voucherId : null,
        currency: order.currencyEnum
      };
      arr.push(ordersCommonDetailsNewOBJ);
    });
    return arr;
  }



  getSubOrderStatus(statusId: number, orderType: string, order: any, chargedOn: number, ordersCommonDetails: IOrderListDataDetails[], isOwner: boolean): any {
    const newState = new GenerateCaseStatus();
    if (orderType === 'Patient') {
      if (statusId in newState.allDefinedStatuses) {
        const subOrderStatus = newState.allDefinedStatuses[statusId];
        if (statusId === 38 &&
          (ordersCommonDetails[0].statusId === 1 || ordersCommonDetails[0].statusId === 2 || ordersCommonDetails[0].statusId === 90)) {
          subOrderStatus.status = 'Awating Model';
        }
        if (!isOwner && (subOrderStatus.status === 'Upload Scans' ||
          subOrderStatus.status === 'Confirm' ||
          subOrderStatus.status === 'Upload IO Scans')) {
          subOrderStatus.hasAction = false;
        }
        return subOrderStatus;
      } else {
        // @ts-ignore
        return newState.allDefinedStatuses['-1'];
      }
    } else {
      if (order.type in newState.miscStatuses) {
        if (chargedOn) {
          newState.miscStatuses[order.type].bgColor = EOrderBackgroundColorStatus.GREEN;
          newState.miscStatuses[order.type].textColor = EOrderTextColorStatus.GREEN;
        }
        return newState.miscStatuses[order.type];
      } else {
        // @ts-ignore
        return newState.miscStatuses['-1'];
      }
    }
  }
  hasVoucher(subOrders: any[]): boolean {
    return subOrders.find((subOrder) => subOrder.commonOrderVoucherHistory?.length > 0) !== undefined;
  }
}
