import {
  Component,
  Input,
  OnInit,
  DoCheck,
  ViewChild,
  OnChanges,
  SimpleChanges,
  Output,
  EventEmitter,
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  FormControl,
  Validators,
} from '@angular/forms';
import { CStaticIds } from '../../../../shared-static-id/shared-static-id';
import { GuideKit } from '../../../../shared-static-data/guide-kits';
import { IGuideKits } from '../../../../shared-interfaces/guideKits';
import { OrderSharedService } from 'shared/shared-services/order-shared.service';
import { MatRadioChange } from '@angular/material/radio';
/**
 * Copyright (c) 3ddx
 *
 * @summary Surgical Guide Form Component
 * @author Basel Elsawy
 *
 * Created at     : 2021
 * Last modified  : 2021-11-24
 */

@Component({
  selector: 'app-surgical-guide',
  templateUrl: './surgical-guide.component.html',
  styleUrls: ['./surgical-guide.component.css'],
})
export class SurgicalGuideComponent implements OnInit, DoCheck, OnChanges {
  @Input() surgicalGuide!: FormGroup;
  @Input() moreThan!: boolean;
  @Input() checkTreatmentPlanAndSurgical!: boolean;
  @Input() restoration!: number;
  @Input() restorationRestType!: number;
  @Input() softwareConversion!: number;
  @Input() buttonOrders: any;
  @Input() softwareTreatmentPlan!: number;
  @ViewChild('extra') extra: any;
  @Output() changeValue: EventEmitter<Function> = new EventEmitter<Function>();
  allExtraComponent: any[] = [
    { name: 'Biomodel', value: 1 },
    { name: 'Bone reduction guide', value: 2 },
    { name: '3DDX loaner Pilot kit', value: 4 },
    { name: 'Fixation pins+drills', value: 5 },
    { name: 'Long stop drills', value: 6 },
    { name: 'IAM', value: 8 },
  ];
  guided: number = 1;
  intraoralScan: number = 1;
  typeOfGuide: number = 1;
  htmlID = CStaticIds;
  minDate!: Date;
  guideKits: IGuideKits[] = GuideKit;
  extraComponents: any[] = [];
  Services: any[] = [
    { name: 'Surgical Guide Design', value: 1 },
    {
      name: 'Surgical Guide and Bone Reduction Guide Design-Magnetix',
      value: 2,
    },
    { name: 'Surgical Guide , Bone Reduction and PMMA Design', value: 4 },
  ];
  selectedService: string = 'Surgical Guide Design';
  guidedSurgeryKitType!: number | null;
  @Input() expressSoftware!: boolean;
  constructor(
    private fb: FormBuilder,
    public orderService: OrderSharedService
  ) {
    this.minDate = new Date();
  }

  ngOnInit(): void {
    this.checkOnDigitalDesignSG();
    this.getGuidedValue();
    this.extraComponents = this.allExtraComponent;
    this.checkExtraComponent(1, 1);
    this.getGuidedSurgeryKitType();
      if(this.surgicalGuide.controls.dds.value){
        this.onSelectDigitalDesign();
      }else{
        this.onSelectNoDigitalDesign();
      }
    }

  /**
   * Checks the status of the digital design in the surgical guide.
   * Subscribes to value changes in the 'dds' control and the 'extracomponents' control
   * in the surgical guide form.
   * Performs actions based on the changes in the control values.
   */
  checkOnDigitalDesignSG() {
    this.surgicalGuide.controls.dds.valueChanges.subscribe((res) => {
      if (res) {
        this.onSelectDigitalDesign();
        if(this.surgicalGuide && this.surgicalGuide.controls.extracomponents.value.length ===0){
          this.surgicalGuide.removeControl('rush');
        }
      } else {
        this.onSelectNoDigitalDesign();
        this.surgicalGuide.addControl(
          'rush',
          new FormControl({ value: 1, disabled: false })
        );
      }
    });

    this.surgicalGuide.controls.extracomponents.valueChanges.subscribe(
      (res) => {
        if (res.length > 0 && !this.surgicalGuide.controls['rush']) {
          this.surgicalGuide.addControl(
            'rush',
            new FormControl({ value: 1, disabled: false })
          );
        } else {
          if (
            this.surgicalGuide.value.dds &&
            this.surgicalGuide.controls['rush'] &&
            res.length === 0
          ) {
            this.surgicalGuide.removeControl('rush');
          }
        }
      }
    );
  }

  ngOnChanges(changes: SimpleChanges) {
    if (
      changes.softwareConversion?.previousValue !=
        changes.softwareConversion?.currentValue ||
      changes.softwareTreatmentPlan?.previousValue !=
        changes.softwareTreatmentPlan?.currentValue
    ) {
      this.ExtraComponents.clear();
      this.surgicalGuide?.controls?.guided.patchValue(1);
    }
  }

  ngDoCheck(): void {
    this.checkExtraComponent(
      this.surgicalGuide.controls.guided.value,
      this.surgicalGuide.controls.typeofGuide.value
    );
    this.getGuidedValue();
    if (
      this.guided === 1 &&
      (this.softwareTreatmentPlan === 4 ||
        this.surgicalGuide.controls.software.value === 4 ||
        this.softwareConversion === 4)
    ) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value === 1 || res.value === 8
      );
    } else if (
      this.guided === 2 &&
      (this.softwareTreatmentPlan === 4 ||
        this.surgicalGuide.controls.software.value === 4 ||
        this.softwareConversion === 4)
    ) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value === 1 || res.value === 5 || res.value === 8
      );
    }
    this.getTypeOfGuide();
  }

  checkExtraComponent(guided: number, typeofGuide: number): void {
    if (guided === 1 && (typeofGuide === 3 || typeofGuide === 4)) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value === 1 || res.value === 8
      );
    } else if (guided === 1 && (typeofGuide === 1 || typeofGuide === 2)) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value !== 2 && res.value !== 5
      );
    } else if (guided === 2 && (typeofGuide === 1 || typeofGuide === 2)) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value !== 2
      );
    } else if (guided === 2 && (typeofGuide === 3 || typeofGuide === 4)) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) => res.value === 1 || res.value === 8 || res.value === 5
      );
    } else if (guided === 3 && (typeofGuide === 1 || typeofGuide === 2)) {
      this.extraComponents = this.allExtraComponent;
    } else if (guided === 3 && (typeofGuide === 3 || typeofGuide === 4)) {
      this.extraComponents = this.allExtraComponent.filter(
        (res) =>
          res.value === 1 ||
          res.value === 8 ||
          res.value === 2 ||
          res.value === 5
      );
    }
  }

  getGuidedSurgeryKitType(): void {
    this.surgicalGuide.controls.guidedSurgeryKitType.valueChanges.subscribe(
      (res) => {
        this.guidedSurgeryKitType = res;
        if (this.guidedSurgeryKitType)
          this.surgicalGuide.controls.typeofGuide.patchValue(5);
        this.getExtraComponentForKitType();
      }
    );
  }

  getExtraComponentForKitType() {
    if (this.guidedSurgeryKitType) {
      if (this.guidedSurgeryKitType >= 23 && this.guidedSurgeryKitType <= 39) {
        this.extraComponents = [];
        this.surgicalGuide.removeControl('extracomponents');
      } else if (this.guidedSurgeryKitType === 6) {
        this.surgicalGuide.addControl('extracomponents', this.fb.array([]));
        this.extraComponents = this.allExtraComponent.filter(
          (res) => res.value === 1 || res.value === 4 || res.value === 8
        );
      } else {
        this.surgicalGuide.addControl('extracomponents', this.fb.array([]));
        this.extraComponents = this.allExtraComponent.filter(
          (res) => res.value === 1 || res.value === 8
        );
        this.guidedSurgeryKitType === 8
          ? this.extraComponents.push({
              name: 'Astra facilitate twist drills',
              value: 7,
            })
          : null;
      }
    }
  }

  onCheckboxChange(e: any) {
    const checkArray: FormArray = this.surgicalGuide.get(
      'extracomponents'
    ) as FormArray;
    if (e.target.checked) {
      if (!checkArray?.value.includes(e.target))
        checkArray?.push(new FormControl(e.target.value));
    } else {
      let i: number = 0;
      checkArray?.controls.forEach((item: any) => {
        if (item.value == e.target.value) {
          checkArray?.removeAt(i);
          return;
        }
        i++;
      });
    }
  }

  get ExtraComponents() {
    return this.surgicalGuide.get('extracomponents') as FormArray;
  }
  get services() {
    return this.surgicalGuide.get('services') as FormArray;
  }
  /**
   * This function get guided input value and adding or removing control intraoralScan depend on guided input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getGuidedValue(): void {
    this.surgicalGuide.controls.guided.valueChanges.subscribe((guided) => {
      this.guided = guided;
      if (this.ExtraComponents) this.ExtraComponents.clear();
      if (this.guided === 1) {
        this.surgicalGuide.addControl(
          'intraoralScan',
          this.fb.control(1, Validators.required)
        );
        this.getIntraoralScanValue();
      } else this.surgicalGuide.removeControl('intraoralScan');
      this.surgicalGuide.controls.typeofGuide.setValue(1);
      const checkArray: FormArray = this.surgicalGuide.get(
        'extracomponents'
      ) as FormArray;

      if (guided == 2 && checkArray.value.includes('2')) {
        checkArray.removeAt(checkArray.value.indexOf('2'));
      }
      if (guided == 1 && checkArray.value.includes('5')) {
        checkArray.removeAt(checkArray.value.indexOf('5'));
      }
    });
  }

  /**
   * This function get intraoral scan input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getIntraoralScanValue(): void {
    this.surgicalGuide.controls.intraoralScan.valueChanges.subscribe(
      (intraoralScan) => {
        this.intraoralScan = intraoralScan;
      }
    );
  }

  /**
   * This function get type of guide input value
   * And adding or removing control (biomodel, boneReductionGuide, fixationPinsDrills, fixationPinsDrills, longStopDrills, iam) depend on type of guide input value
   *
   * @author Basel Elsawy
   * @copyright 3ddx
   */
  getTypeOfGuide(): void {
    this.surgicalGuide.controls.typeofGuide.valueChanges.subscribe(
      (typeOfGuide) => {
        if (typeOfGuide !== 5) {
          this.surgicalGuide?.controls?.guidedSurgeryKitType.patchValue(0);
        }
        this.typeOfGuide = typeOfGuide;
        if (this.ExtraComponents) {
          this.ExtraComponents.clear();
        } else {
          this.surgicalGuide.addControl('extracomponents', this.fb.array([]));
        }

        if (this.typeOfGuide == 5) {
          this.getExtraComponentForKitType();
        }
      }
    );
  }

  checkSoftwareChanges($event: MatRadioChange): void {
    if (
      !this.orderService.buttons.treatmentPlan &&
      this.orderService.buttons.surgicalGuide
    ) {
      this.surgicalGuide?.controls?.typeofGuide.patchValue(1);
      this.surgicalGuide?.controls?.guidedSurgeryKitType.patchValue(null);
      this.surgicalGuide?.controls?.guided.patchValue(1);
      this.surgicalGuide?.controls?.intraoralScan.patchValue(1);
      if (this.ExtraComponents) {
        this.ExtraComponents.clear();
      }
    }
  }

  /*
   *  Removes the controls dependant on DD form the surgicalGuide FormGroup
   *  when the 'No Digital Design' option is selected.
   */
  onSelectNoDigitalDesign() {
    this.surgicalGuide.removeControl('printerGroup');
    this.surgicalGuide.removeControl('deliverablesgroup');
    this.surgicalGuide.removeControl('services');
  }

  /*
   *  adds the controls dependant on DD to the surgicalGuide FormGroup
   *  when the 'Digital Design' option is Yes.
   */
  onSelectDigitalDesign() {
    this.surgicalGuide.addControl('services', new FormControl(this.restorationRestType != 2 ? 1 : 3));
    this.surgicalGuide.addControl('printerGroup', new FormGroup({}));
    this.surgicalGuide.addControl('deliverablesgroup', new FormGroup({}));
  }
}
