import {Injectable} from '@angular/core';
import {DataService} from '../../DataService/data.service';
import {IApiData} from '../../../Models/Interfaces/APIs/apiData.model';
import {ISurgicalGuide} from '../../../Models/Interfaces/Order/SurgicalGuide/surgical-guide';
import {Observable} from 'rxjs';
import {IMainResponse} from '../../../Models/Interfaces/Main/MainResponse';
import {APIName} from '../../../../../../../shared/API/APIName';
import {IConfirmSurgicalGuide} from '../../../Models/Interfaces/Order/SurgicalGuide/confirm-surgical-guide';

@Injectable({
  providedIn: 'root'
})
export class SurgicalGuideService {
  /**
   * The Requirements For The Construction Of The service
   * @param dataService for managing Http methods
   */
  constructor(private dataService: DataService) {
  }
  /**
   * get surgical guide data
   * @param surgicalGuideOBJ surgical guide object
   * @return Observable<IMainResponse>
   */
  getSurgicalGuideData(surgicalGuideOBJ: IApiData<ISurgicalGuide>): Observable<IMainResponse> {
    return this.dataService.post(APIName.orderDetailsActions.getSurgicalGuide, surgicalGuideOBJ);
  }
  /**
   * confirm surgical guide
   * @param confirmSurgicalGuide confirm surgical guide object
   * @return Observable<IMainResponse>
   */
  confirmSurgicalGuide(confirmSurgicalGuide: IApiData<IConfirmSurgicalGuide>): Observable<IMainResponse> {
    return this.dataService.post(APIName.orderDetailsActions.confirmSurgicalGuide, confirmSurgicalGuide);
  }
}
