import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';
import {FileSystemDirectoryEntry, FileSystemFileEntry, NgxFileDropEntry} from 'ngx-file-drop';
import {CStaticIds} from '../../shared-static-id/shared-static-id';
import {OrderListService} from '../../../projects/connect/src/app/Services/OrderServices/OrderListService/order-list.service';
import {SubOrderType} from '../../shared-static-data/SubOrderType';

@Component({
  selector: 'app-upload-file',
  templateUrl: './upload-file.component.html',
  styleUrls: ['./upload-file.component.css']
})
export class UploadFileComponent implements OnInit {
  public files: NgxFileDropEntry[] = [];
  file!: File;
  isAllowedFile: boolean = false;
  errorMessage: string = '';
  htmlID = CStaticIds;
  constructor(
    public dialogRef: MatDialogRef<UploadFileComponent>, private orderService: OrderListService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
  }

  ngOnInit(): void {
  }

  cancel(): void {
    this.dialogRef.close();
  }


  public dropped(files: NgxFileDropEntry[]): void {
    this.files = files;
    for (const droppedFile of files) {
      if (droppedFile.fileEntry.isFile && this.isFileAllowed(droppedFile.fileEntry.name.toLocaleLowerCase()).allowed) {
        const fileEntry = droppedFile.fileEntry as FileSystemFileEntry;
        fileEntry.file((file: File) => {
          this.file = file;
        });
      } else {
        this.errorMessage = this.isFileAllowed(droppedFile.fileEntry.name.toLocaleLowerCase()).message;
      }
    }
  }

  isFileAllowed(fileName: string): { allowed: boolean, message: string } {
    this.isAllowedFile = false;
    const allowedFiles = ['.zip', '.png', '.jpg', '.stl', '.rar' , '.7z', '.gz'];
    const regex = /(?:\.([^.]+))?$/;
    const extension = regex.exec(fileName);
    if (undefined !== extension && null !== extension) {
      this.isAllowedFile = true;
    }
    return {
      allowed: this.isAllowedFile,
      message: this.isAllowedFile ? '' : 'File Not Allowed Must Be ' + allowedFiles.map(c => c)
    };
  }

  onSubmit(): void {
    if (!this.file && !this.isAllowedFile) {
      return;
    }
    this.dialogRef.close({file: this.file});
  }

}
