import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialog, MatDialogRef} from '@angular/material/dialog';
import {AbstractControl, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {SubOrderType} from '../../../../shared-static-data/SubOrderType';
import {MatCheckboxChange} from '@angular/material/checkbox';
import {CStaticIds} from '../../../../shared-static-id/shared-static-id';

@Component({
  selector: 'app-send-to-friend',
  templateUrl: './send-to-friend.component.html',
  styleUrls: ['./send-to-friend.component.css']
})
export class SendToFriendComponent implements OnInit {
  sendToForm!: FormGroup;
  sharedOrderEnum: number[] = [];
  SubOrderType = SubOrderType;
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param {MatDialogRef<SendToFriendComponent>} dialogRef For The Dialogue Reference
   * @param dialog For The Dialogue
   * @param fb for form builder
   * @param data
   */
  constructor(public dialog: MatDialog, public dialogRef: MatDialogRef<SendToFriendComponent>,
              private fb: FormBuilder,
              @Inject(MAT_DIALOG_DATA) public data: any) {
  }

  ngOnInit(): void {
    this.sendToForm = this.fb.group({
      friendMail: ['', [Validators.required, Validators.email]],
      friendName: ['', [Validators.required]],
      modelWork: [{value: this.onCheck(SubOrderType.INTRAORAL_SCAN.name , SubOrderType.LASER_SCAN.name), disabled: !this.onCheck(SubOrderType.INTRAORAL_SCAN.name , SubOrderType.LASER_SCAN.name)}],
      conversion: [{value: this.onCheck(SubOrderType.CONVERSION.name), disabled: !this.onCheck(SubOrderType.CONVERSION.name)}],
      treatment: [{value: this.onCheck(SubOrderType.TREATMENT_PLAN.name), disabled: !this.onCheck(SubOrderType.TREATMENT_PLAN.name)}],
      surgical: [{value: this.onCheck(SubOrderType.SURGICAL_GUIDE.name), disabled: !this.onCheck(SubOrderType.SURGICAL_GUIDE.name)}],
      reports: [{value: this.onCheck(SubOrderType.REPORT.name), disabled: !this.onCheck(SubOrderType.REPORT.name)}],
      restoration: [{value: this.onCheck(SubOrderType.RESTORATION.name , SubOrderType.GFMR.name), disabled: !this.onCheck(SubOrderType.RESTORATION.name, SubOrderType.GFMR.name)}],
      sharedOrderEnum: [this.getSharedOrders(), [Validators.required, this.validateSharedOrderEnum]]
    });
  }
  /**
   * check order type
   * @param orderType order type
   * @param orderType2
   * @return boolean
   */
  onCheck(orderType: string, orderType2?: string): boolean {
    const arr =
      this.data.order.subOrders.filter((order: { subOrderStatus: { subOrderName: string; }; }) =>
        order.subOrderStatus.subOrderName === orderType ||  order.subOrderStatus.subOrderName === orderType2);
    return arr.length > 0;
  }
  /**
   * get shared orders
   * @return number[]
   */
  getSharedOrders(): number[] {
    this.sharedOrderEnum = this.data.order.subOrders.map((c: { subOrderStatus: { subOrderId: any; }; }) => c.subOrderStatus.subOrderId);
    return this.sharedOrderEnum;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }
  /**
   * show options
   * @param event MatCheckboxChange
   * @param value
   * @return void
   */
  showOptions(event: MatCheckboxChange, value: number, value2?: number): void {
    if (event.checked) {
      this.sharedOrderEnum.push(value);
      if (value2) { this.sharedOrderEnum.push(value2)};
    } else {
      const index = this.sharedOrderEnum.findIndex(c => c === value || c === value2);
      this.sharedOrderEnum.splice(index, 1);
    }
    this.sendToForm.controls.sharedOrderEnum.updateValueAndValidity();
  }
  /**
   * validate shared order enum
   * @param control Abstract control
   * @return any
   */
  validateSharedOrderEnum(control: AbstractControl): any {
    if (control.value.length === 0) {
      return {invalidSharedOrderEnum: true};
    }
    return null;
  }

  onSubmit(): void {
    this.dialogRef.close(this.sendToForm.value);
  }
}
