import {Component, EventEmitter, HostBinding, Input, OnInit, Output, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {MyErrorStateMatcher} from '../../../shared-functions/state-matcher';
import {ISharedLogin} from '../../../shared-interfaces/login';
import {MatDialog} from '@angular/material/dialog';
import {ForgetPasswordComponent} from '../../forget-password/forget-password.component';
import {CStaticIds} from '../../../shared-static-id/shared-static-id';

/**
 * Copyright (c) 3ddx
 *
 * @summary Add Order Form Component
 * @author Basel Elsawy && Amr Mossad
 *
 * Created at     : 2021
 * Last modified  : 2021-10-28
 */
@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class LoginFormComponent implements OnInit {
  signInForm!: FormGroup;
  matcher!: MyErrorStateMatcher;
  submitted: boolean = false;
  @Input() logo: string = '';
  @Output() signInOBJ: EventEmitter<ISharedLogin> = new EventEmitter();
  @Output() forgetPasswordOBJ: EventEmitter<{ email: string }> = new EventEmitter();
  form = this.fb.group({
    verby: [{value: '', disabled: true}]
  });
  htmlID = CStaticIds;
  /**
   * The Requirements For The Construction Of The Component
   * @param dialog for angular material dialog
   * @param fb for using FormBuilder
   */
  constructor(private fb: FormBuilder, public dialog: MatDialog) {
  }

  ngOnInit(): void {
    this.initForm();
  }


  /**
   * This function creat init form group
   * @return void
   */
  initForm(): void {
    this.signInForm = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required]],
      rememberMe: [false],
    });
    this.matcher = new MyErrorStateMatcher();
  }

  /**
   * submit form and output data to sign in component
   * @return void
   */
  onSubmit(): void {
    this.submitted = true;
    if (this.signInForm.valid) {
      this.signInOBJ.emit(this.signInForm.value);
    }
  }
  /**
   * open forget password pop-up and output data
   * @return void
   */
  onForgetPassword(): void {
    const dialogRef = this.dialog.open(ForgetPasswordComponent, {
      minWidth: '40vw',
      maxWidth: '80vw',
      maxHeight: '90vh',
      data: {
        logo: this.logo,
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.forgetPasswordOBJ.emit(result);
      }
    });
  }
}
