import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material/dialog';

@Component({
  selector: 'app-upload-missing-files',
  templateUrl: './upload-missing-files.component.html',
  styleUrls: ['./upload-missing-files.component.css']
})
export class UploadMissingFilesComponent implements OnInit {
  dragFiles: any;
  validComboDrag: any;
  lastInvalids: any;
  fileDropDisabled: any;
  maxSize: any;
  baseDropValid: any;
  accept = '*';
  files: File[] = [];
  hasBaseDropZoneOver = false;
  lastFileAt: any;
  logo = 'assets/Images/connectLogo.png';
  /**
   * The Requirements For The Construction Of The Component
   * @param {MatDialogRef<UploadMissingFilesComponent>} dialogRef For The Dialogue Reference
   * @param data
   */
  constructor(public dialogRef: MatDialogRef<UploadMissingFilesComponent>, @Inject(MAT_DIALOG_DATA) public data: any) { }

  ngOnInit(): void {
  }
  getDate(): Date{
    return new Date();
  }

  onClosingDialoge(): void {
    this.dialogRef.close();
  }
}
